@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.toolbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.toolbarLeft {
  order: unset;
}

.toolbarRight {
  order: unset;
  width: unset;
}

.toolbarReversedMaxMedium {
  composes: toolbar;
}
@media (max-width: 600px) {
  .toolbarReversedMaxMedium {
    flex-direction: column;
    justify-content: center;
  }
  .toolbarReversedMaxMedium .toolbarLeft {
    order: 2;
  }
  .toolbarReversedMaxMedium .toolbarRight {
    order: 1;
    width: 100%;
  }
}