@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.cartDrawer {
  position: fixed;
  z-index: 3;
  pointer-events: none;
  top: var(--header-height);
  right: 0;
  left: 0;
  bottom: 0;
  height: calc(100vh - var(--header-height));
  width: 100%;
}
.cartDrawerMobile {
  position: fixed;
  bottom: 0;
  height: auto;
  width: 100%;
  z-index: 3;
  left: 0;
}

.wrapper {
  width: 100%;
  pointer-events: all;
  max-width: 418px;
  background-color: #e9edf6;
  margin-right: 0;
  margin-left: auto;
  height: calc(100vh - var(--header-height));
  display: flex;
  flex-direction: column;
}
.wrapperMobile {
  z-index: 100;
  background-color: #e9edf6;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #e9edf6;
  padding: 1rem 2rem;
  border-bottom: 1px solid #aeb6c2;
}
.headerText {
  font-weight: 700;
  font-size: 1rem;
  font-family: "museo-sans", sans-serif;
}

.closeButton {
  padding: 0 !important;
}
.closeButton:hover > i::before {
  color: #000083;
}
.closeButton > i::before {
  font-size: 1.25rem;
  color: #0a101a;
}
.closeButton:hover > i::before {
  color: #000083;
}

.itemsDesktop {
  position: relative;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  padding: 0 2rem 1rem;
  box-sizing: border-box;
  background: linear-gradient(to top, rgba(255, 255, 255, 0), #e9edf6) top, linear-gradient(rgba(255, 255, 255, 0), #e9edf6) bottom, linear-gradient(to top, rgba(104, 108, 114, 0) 26.56%, rgba(104, 108, 114, 0.105) 100%) top, linear-gradient(rgba(104, 108, 114, 0) 26.56%, rgba(104, 108, 114, 0.105) 100%) bottom;
  background-repeat: no-repeat;
  background-size: 100% 40px, 100% 40px, 100% 14px, 100% 14px;
  background-attachment: local, local, scroll, scroll;
}
.itemsDesktop::-webkit-scrollbar {
  width: 6px;
  background: transparent;
}
.itemsDesktop::-webkit-scrollbar-track {
  background: transparent;
}
.itemsDesktop::-webkit-scrollbar-thumb {
  background: #aeb6c2;
  border-radius: 5px;
}
.itemsDesktop::-webkit-scrollbar-thumb:hover {
  background: #8d97a8;
}

.itemsMobile {
  position: relative;
  padding: 0 2rem 1rem;
}

.emptyCartMessage {
  text-align: center;
  color: #0a101a;
  font-size: 1.5rem;
  margin: 0;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
.emptyCartMessageMobile {
  font-size: 1.25rem;
  color: #0a101a;
  padding: 40px 0;
  margin: 0 auto;
  text-align: center;
}

.noItemsInCart {
  height: calc(100% - 185px);
  background: #f2f4fa;
  padding-bottom: 0;
}