@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.wrapper {
  position: absolute;
  padding: 1rem;
  border: 3px solid #f5f4f2;
  border-radius: 32px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  z-index: 100;
}
.wrapper.noSpacing {
  overflow: hidden;
  padding: 0;
  gap: 0;
}
.wrapper.small {
  border-radius: 16px;
  border-width: 1px;
}
.wrapper.large {
  padding: 1.5rem;
}
.wrapper .header {
  margin-left: 1rem;
  display: flex;
  align-items: center;
  min-height: 35px;
}
.wrapper .header .action {
  margin-left: auto;
}
.wrapper .content {
  padding: 1rem;
  border-radius: 16px;
  background-color: #fafaf9;
}
.wrapper .content .header {
  color: #505f6b;
  margin-left: 0;
  margin-bottom: 1rem;
}
.wrapper .content .header .action {
  color: #505f6b;
}
.wrapper .content .header .icon {
  color: #505f6b;
  margin-right: 0.375rem;
}
.wrapper .header:not(:has(> *)) {
  display: none;
}