@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.footerContainer {
  background-color: #18263d;
  padding-top: 3rem;
  position: relative;
  overflow: visible;
}
@media (max-width: 600px) {
  .footerContainer {
    padding-top: 0;
  }
  .footerContainer .footerBottomRow {
    border: none;
  }
}
.footerContainer .legalLinks a {
  white-space: nowrap;
  text-wrap: nowrap;
  font-weight: 300;
}
.footerContainer .footerBottomRow .bottomRowContent {
  padding-top: 1.5rem;
  padding-bottom: 2.5rem;
  margin-inline: auto;
  display: flex;
  gap: 2rem;
}
@media (max-width: 1064px) {
  .footerContainer .footerBottomRow .bottomRowContent {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.footerContainer .footerBottomRow .bottomRowContent .platformLinks {
  gap: 3rem;
}
@media (max-width: 600px) {
  .footerContainer .footerBottomRow .bottomRowContent .platformLinks {
    display: grid;
    grid-template-columns: 1fr 1fr;
    text-align: center;
    padding-top: 0.78125rem;
    padding-bottom: 0.78125rem;
  }
}
.footerContainer .footerBottomRow .bottomRowContent .platformLinks a {
  margin-right: 0;
}
.footerContainer .footerBottomRow .bottomRowContent .platformLinks .luluLogo {
  width: 75px;
}
.footerContainer .footerBottomRow .bottomRowContent .platformLinks .apiLogo {
  width: 108px;
  margin-top: 5px;
}
.footerContainer .footerBottomRow .bottomRowContent .platformLinks .jrLogo {
  width: 77px;
}
.footerContainer .footerBottomRow .bottomRowContent .platformLinks .theTiltLogo {
  width: 102px;
  height: 26px;
}
.footerContainer .footerBottomRow .bottomRowContent .legalInfoContainer {
  justify-content: center !important;
  gap: 1.5rem;
  padding-top: 0;
}
.footerContainer .footerBottomRow .bottomRowContent .legalInfoContainer .legalInfo {
  margin-right: 0;
}
.footerContainer .footerBottomRow .bottomRowContent .legalInfoContainer .legalInfo .legalLinks {
  display: flex;
  gap: 1.5rem;
}
.footerContainer .footerBottomRow .bottomRowContent .legalInfoContainer .legalInfo .legalLinks a {
  padding-inline: 0;
  margin-right: 0;
  font-size: 0.75rem;
}
.footerContainer .footerBottomRow .bottomRowContent .legalInfoContainer .legalInfo .copyrightMessage {
  text-align: left;
  font-size: 0.75rem;
  line-height: 133%;
}
.footerContainer .footerBottomRow .bottomRowContent .legalInfoContainer div:nth-child(2) {
  display: flex;
  align-items: center;
}
.footerContainer .footerBottomRow .bottomRowContent .legalInfoContainer div:nth-child(2) a {
  padding-inline: 0;
}

.footerTopRow {
  display: grid;
  grid-gap: 4.5rem;
  grid-template-columns: 2fr 1fr;
}
@media (max-width: 600px) {
  .footerTopRow {
    padding-inline: 0;
  }
}
@media (max-width: 1024px) {
  .footerTopRow {
    display: block;
  }
}

.mainNavLinks {
  font-family: "museo-sans", sans-serif;
  display: flex;
  margin-bottom: 2rem;
  gap: 0.5rem;
  flex-wrap: wrap;
  justify-content: flex-start;
}
@media (min-width: 1024px) {
  .mainNavLinks {
    margin-left: -1rem;
  }
}
@media (max-width: 600px) {
  .mainNavLinks {
    display: flex;
    flex-direction: column;
    gap: 0;
  }
  .mainNavLinks .footerLink {
    font-size: 1rem;
  }
  .mainNavLinks > * {
    justify-content: space-between;
    padding: 1.125rem 1.5rem !important;
    text-align: left;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  }
}

.footerLink {
  color: rgba(255, 255, 255, 0.8);
}
.footerLink:hover {
  color: #fff;
}

.mainNavLink {
  font-family: "museo-sans", sans-serif;
  font-size: 1rem;
  line-height: 150%;
}
@media (max-width: 600px) {
  .mainNavLink {
    font-size: 0.75rem;
    margin-right: 0;
    flex-basis: 33.33%;
  }
}

.platformLinks {
  display: flex;
  align-items: center;
}
@media (max-width: 480px) {
  .platformLinks {
    display: grid;
    grid-template-columns: 1fr 1fr;
    text-align: center;
  }
}
.platformLinks a {
  margin-right: 3rem;
}
@media (max-width: 1024px) {
  .platformLinks a {
    margin-right: 2.5rem;
  }
}
@media (max-width: 840px) {
  .platformLinks a {
    margin-right: 0;
  }
}
@media (max-width: 840px) {
  .platformLinks {
    justify-content: space-evenly;
  }
}

.footerSelect {
  position: relative;
  min-width: auto;
  padding-bottom: 0;
  padding-inline: 1rem;
}
.footerSelect svg {
  transition: 0.1s;
}
.footerSelect svg path {
  fill: rgba(255, 255, 255, 0.8);
}
.footerSelect .footerLink {
  padding-inline: 0 !important;
}
.footerSelect .footerSelected {
  gap: 0.25rem;
}
@media (max-width: 600px) {
  .footerSelect.active {
    padding-bottom: 1rem !important;
  }
}
.footerSelect.active svg {
  transform: scaleY(-1);
}
.footerSelect.active svg path {
  fill: #fff;
}
@media (max-width: 600px) {
  .footerSelect > .footerSelected {
    justify-content: space-between;
  }
}

.socialLinksContainer {
