    width: fit-content;
  }
}
.socialLinks a {
  text-align: center;
  color: #aeb6c2;
  font-size: 32px;
  padding: 1px;
  border-radius: 50%;
  height: 32px;
  width: 32px;
  display: grid;
  align-items: center;
  justify-content: center;
  transition: 0.3s;
}
.socialLinks a:hover {
  text-decoration: none;
  color: #fff;
}

.subscribeContainer {
