.legalInfoContainer {
  display: flex;
  font-family: "museo-sans", sans-serif;
  padding-top: 1.5rem;
}
@media (max-width: 840px) {
  .legalInfoContainer {
    justify-content: flex-end;
  }
}
@media (max-width: 480px) {
  .legalInfoContainer {
    justify-content: center;
  }
}

.legalInfo {
  text-align: right;
  line-height: 133%;
