.footerSelected {
  align-items: flex-start;
  display: flex;
  justify-content: center;
  align-items: center;
}

.linkCollection {
  position: relative;
  display: flex;
  line-height: 150%;
  justify-content: center;
  box-sizing: border-box;
  align-items: center;
  padding-bottom: 1rem;
}
.linkCollection > a, .linkCollection > span {
  margin-right: 0.5rem;
}
.linkCollection svg {
  transition: 0.1s;
}
.linkCollection svg path {
  fill: rgba(255, 255, 255, 0.8);
}
.linkCollection.active a,
.linkCollection.active span {
  text-decoration: none;
}
.linkCollection.active svg {
  transform: scaleY(-1);
}
.linkCollection.active svg path {
  fill: #fff;
}
@media (max-width: 600px) {
  .linkCollection {
    padding: 0;
    justify-content: space-between;
  }
}
@media (min-width: 600px) {
  .linkCollection {
    padding-inline: 1rem;
    padding-bottom: 0;
    gap: 0.25rem;
  }
  .linkCollection .footerLink {
    padding-inline: 0 !important;
  }
}

.dropdownMenu {
  position: absolute;
  width: 160px;
  z-index: 1;
}
.dropdownMenu.up {
  bottom: 100%;
}
.dropdownMenu .content {
  border-radius: 16px;
  border: 1px solid #d1d6e0;
  background-color: #fff;
  overflow: hidden;
}

.dropdownMenu > .content > .title {
  padding: 1rem 1rem 0.5rem;
  color: #0a101a;
  font-size: 10px !important;
  font-weight: 300;
  line-height: 115%;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: default;
}

.flyoutLink,
.dropdownMenu > .content > .link {
  padding: 0.75rem 1.5rem !important;
  color: #0a101a;
  font-size: 0.875rem;
  font-weight: 500 !important;
  line-height: 125%;
  cursor: pointer;
  text-wrap: balance !important;
}
.flyoutLink:hover,
.dropdownMenu > .content > .link:hover {
  color: #0a101a;
  background: #e9edf6;
  text-decoration: none;
}
.flyoutLink:first-of-type,
.dropdownMenu > .content > .link:first-of-type {
  padding: 1rem 1.5rem 0.75rem !important;
}
.flyoutLink:last-child,
.dropdownMenu > .content > .link:last-child {
  padding: 0.75rem 1.5rem 1rem !important;
}

.footerLink {
  margin: 0 !important;
  padding-inline: 0;
  text-wrap: nowrap;
  font-weight: 700;
}
.footerLink:hover {
  text-decoration: none !important;
}
.footerLink.footerLink.mainNavLink {
  padding: 0.5rem 1rem;
}
@media (max-width: 600px) and (pointer: coarse) {
  .footerLink.footerLink.mainNavLink {
    color: #fff;
  }
}
@media (max-width: 600px) {
  .footerLink.footerLink.mainNavLink {
    padding: 0;
  }
}

.activeLink {
  color: #fff;
}

.accordionContent {
  max-height: 0;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  max-height: 0;
  transition: max-height 0.3s ease-out;
  overflow: hidden;
}
.accordionContent a,
.accordionContent div {
  color: rgba(255, 255, 255, 0.8);
  padding: 0.5rem 0 !important;
  cursor: pointer;
  transition: 0.2s;
  font-size: 0.875rem;
}
.accordionContent a:first-child,
.accordionContent div:first-child {
  margin-top: 1.125rem !important;
}
@media (pointer: coarse) {
  .accordionContent a,
  .accordionContent div {
    color: #fff;
  }
}
.accordionContent a:hover,
.accordionContent div:hover {
  color: #fff;
}
.accordionContent.active {
  transition: max-height 0.3s ease-in;
}
.accordionContent .accordionLink {
  font-weight: 500;
  line-height: 125%;
  text-wrap: balance;
  font-size: 0.875rem;
}
.accordionContent .accordionLink:hover {
  text-decoration: none;
}

.footerLinkCollection {
  position: relative;
}
@media (max-width: 600px) {
  .footerLinkCollection {
    flex-basis: 33.33%;
  }
}
@media (max-width: 600px) {
