@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.errorPage {
  height: 100vh;
  width: 100%;
  background-color: #e9edf6;
}

.container {
  max-width: 28.875rem;
  margin: 0 auto;
  text-align: center;
}

.logo {
  width: 8rem;
  margin: 3rem 0 3.5rem;
}

.header {
  margin: 0;
  margin-bottom: 1rem;
  line-height: 1.08;
}

.description {
  line-height: 1.4;
  font-size: 1.25rem;
  color: rgba(10, 16, 26, 0.6);
  font-weight: 300;
  margin: 0;
  margin-bottom: 1rem;
}