@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.header {
  background-color: #000083;
  color: #fff;
  padding: 0.5rem 1rem;
  text-align: left;
}

.headerYear {
  font-size: 12px;
  font-stretch: normal;
  font-style: normal;
  font-weight: 500;
  letter-spacing: normal;
  line-height: 1;
  margin-bottom: 4px;
}

.headerDate {
  font-size: 20px;
  font-stretch: normal;
  font-style: normal;
  font-weight: 300;
  letter-spacing: normal;
  line-height: 1;
}

.headerMonthSelect {
  background-color: #fff;
  color: #000083;
  display: flex;
  font-size: 1rem;
  font-stretch: normal;
  font-style: normal;
  font-weight: 500;
  letter-spacing: normal;
  line-height: 1;
  justify-content: space-between;
  margin: 1rem 0;
}

.arrow {
  cursor: pointer;
}

.monthPickerHeader {
  background-color: #000083;
  color: #fff;
  display: flex;
  font-size: 20px;
  font-stretch: normal;
  font-style: normal;
  font-weight: 300;
  letter-spacing: normal;
  line-height: 1;
  justify-content: space-between;
  padding: 1rem;
  margin-bottom: 1.5rem;
}