@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.filterBar {
  background-color: #000083;
  border-radius: 0.125rem;
  padding: 1rem;
  padding-top: 0;
  margin-bottom: 2rem;
}
.filterBar .header {
  display: flex;
  justify-content: space-between;
}
.filterBar .header .content {
  width: 100%;
}
.filterBar .header .toggle {
  padding: 2rem 2.75rem 0 3.75rem;
  margin-bottom: auto;
}
@media (max-width: 840px) {
  .filterBar .header .toggle {
    padding: 2rem 1rem 0 2rem;
  }
}
@media (max-width: 600px) {
  .filterBar .header .toggle {
    padding: 2rem 0.5rem 0 1.5rem;
  }
}
@media (max-width: 480px) {
  .filterBar .header .toggle {
    padding: 2rem 0 0 1rem;
  }
}
.filterBar .header .toggle i {
  font-size: 2rem;
}
.filterBar .header .toggle i:hover {
  cursor: pointer;
}
@media (max-width: 600px) {
  .filterBar .header .toggle i {
    font-size: 1.5rem;
  }
}
.filterBar .header .actions {
  margin-top: 0.5rem;
}