@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.bookRadio {
  border: 1px solid #d1d6e0;
  border-radius: 0.125rem;
}
.bookRadio:hover {
  cursor: pointer;
  border-color: #000083;
}
.bookRadioImage {
  width: 100%;
  position: relative;
}
.bookRadioImage img {
  width: 100%;
  border-bottom: 1px solid #d1d6e0;
}
.bookRadioFooter {
  padding: 1rem;
  display: flex;
}
.bookRadioContentIcon {
  display: block;
  margin-top: 0.5rem;
  color: #008f62;
  font-size: 1.5rem;
}
.bookRadioContentLabel {
  color: #0a101a;
  cursor: pointer;
  font-weight: bold;
  line-height: normal;
}
@media (max-width: 480px) {
  .bookRadioContentLabel {
    font-size: 0.875rem;
  }
}
.bookRadioContentDescription {
  color: rgba(10, 16, 26, 0.6);
  font-size: 0.75rem;
  line-height: 1.33;
  height: 6rem;
  margin: 0.5rem 0 0;
}
.bookRadioButton {
  padding: 0 !important;
  box-shadow: none !important;
  width: 100%;
  display: block;
  border: 0.0625rem solid #000083;
  border-radius: 0 0 0.125rem 0.125rem;
}
.bookRadioButton i {
  font-size: 1.5rem;
  transform: rotate(180deg);
}
.bookRadioSelected {
  background-color: #e9edf6;
  border-color: #000083;
  position: relative;
}
.bookRadioSelected::before {
  content: "";
  height: 100%;
  width: 0.5rem;
  background-color: #94d232;
  position: absolute;
  left: 0;
  top: 0;
  border-top-left-radius: 0.125rem;
  border-bottom-left-radius: 0.125rem;
  z-index: 1;
}
.bookRadioHasIcon {
  padding-bottom: 0 !important;
}
.bookRadioDisabled {
  pointer-events: none;
}
.bookRadioDisabled::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.6);
}
.bookRadioDisabledContent {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.bookRadioDisabledContent * {
  background-color: #fff;
  border: 1px solid #d1d6e0;
  border-radius: 0.125rem;
}
.bookRadioDisabledContent span {
  color: rgba(10, 16, 26, 0.7);
  line-height: 1.5;
  padding: 0.25rem 0.5rem;
}
@media (max-width: 480px) {
  .bookRadioDisabledContent span {
    font-size: 0.875rem;
  }
}
.bookRadioDisabledContent i {
  box-sizing: content-box;
  color: #8d97a8;
  font-size: 21px;
  height: 1.875rem;
  width: 1.875rem;
  border-radius: 1.875rem;
  text-align: center;
  line-height: 1.75rem;
  margin-bottom: 0.5rem;
}

.container {
  max-width: 18.75rem;
  position: relative;
  background-color: #fff;
}
@media (max-width: 480px) {
  .container {
    width: 100%;
  }
}
.containerHasDropdown {
  width: 13.8rem;
}
.containerHasDropdown .bookRadioContentDescription {
  display: none;
}
.containerHasDropdown .bookRadio {
  padding-bottom: 1.875rem;
  border-bottom: 0;
  border-radius: 0.125rem 0.125rem 0 0;
}
@media (max-width: 840px) {
  .containerHasDropdown {
    width: 11.25rem;
  }
}
.containerOpened .bookRadio {
  padding-bottom: 0;
}
.containerOpened .bookRadioContentDescription {
  display: block;
  padding: 0 1rem 1rem 1rem;
  margin: 0;
}
.containerOpened .bookRadioButton {
  background-color: #fff;
}
.containerOpened .bookRadioButton i {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000083;
}
.containerConfigurator {
  display: flex;
  width: 14rem;
}
.containerConfigurator .bookRadio {
  height: auto;
  width: 100%;
}