.nakedButton {
  appearance: none;
  -webkit-tap-highlight-color: transparent;
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  color: inherit;
  font: inherit;
  user-select: none;
  text-align: inherit;
  cursor: pointer;
  outline-offset: 1px;
}
.nakedButton:disabled {
  cursor: unset;
}