@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.imageTextBullet {
  text-align: center;
  display: block;
  align-items: center;
  height: 100%;
}

.image {
  width: 6.875rem;
}
@media (max-width: 600px) {
  .image.smaller {
    width: 4.5rem;
  }
}
@media (min-width: 600px) {
  .image {
    width: 8.375rem;
  }
}
@media (min-width: 1024px) {
  .image {
    width: 10.25rem;
  }
}

.imageContainer {
  position: relative;
}

.title {
  margin: 0;
  margin-bottom: 0.5rem;
}
.title.link {
  font-weight: 500;
}

.description {
  margin: 0;
  margin-bottom: 1rem;
}