@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container {
  margin-top: 1rem;
  position: relative;
}

.dropdown {
  border-bottom-left-radius: 0.125rem;
  border-bottom-right-radius: 0.125rem;
  margin-top: -2px;
  padding: 2.5rem 5rem 3rem 2.5rem;
  background-color: #e9edf6;
  overflow-x: scroll;
  scrollbar-width: none;
}
.dropdown::-webkit-scrollbar {
  display: none;
}

.footnote {
  font-family: "interface", sans-serif;
  font-size: 0.75rem;
  line-height: 1rem;
  position: relative;
  margin-top: 0.3rem;
  margin-left: 2rem;
  color: rgba(10, 16, 26, 0.7);
}
.footnote .text {
  position: absolute;
  top: 0;
  left: -0.5rem;
}

.helperLink {
  font-size: 0.75rem;
  margin-left: 2rem;
  display: inline-block;
  margin-top: 0.3rem;
  text-decoration: underline;
  font-family: "interface", sans-serif;
  font-weight: 300;
}