@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.accordionForm {
  position: relative;
}
.accordionForm.disabled * {
  opacity: 0.8;
}
.accordionForm .headingContentWrapper {
  width: 100%;
}
.accordionForm .titleContainer {
  align-items: center;
  display: flex;
  justify-content: space-between;
}
.accordionForm .titleContainer i {
  margin-left: 1rem;
}
.accordionForm .titleAccessoryContainer {
  line-height: 0;
}
.accordionForm .dropdown {
  border: 1px solid #000083;
  border-bottom-left-radius: 0.125rem;
  border-bottom-right-radius: 0.125rem;
  border-top-style: dashed;
  display: none;
  overflow: hidden;
}
.accordionForm .dropdown .open {
  display: block;
}
.accordionForm .content {
  align-items: center;
  background-color: #e9edf6;
  border: 1px solid #d1d6e0;
  border-radius: 0.125rem;
  cursor: pointer;
  display: flex;
}
.accordionForm .content .inputSection {
  margin-left: 1rem;
  min-width: 16px;
  width: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.accordionForm .content .inputSection:empty {
  display: none;
}
.accordionForm .content .main {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  flex-grow: 1;
  height: 100%;
}
.accordionForm .content .main .heading {
  display: flex;
  gap: 1rem;
  align-items: center;
  padding: 1rem;
  width: 100%;
}
.accordionForm .content .main .icon {
  display: flex;
  align-items: center;
  background-color: #000083;
  color: #fff;
  padding: 0 0.5rem;
}
.accordionForm .content .input {
  cursor: pointer;
}
.accordionForm .content .input:disabled {
  cursor: default;
}
.accordionForm .content .label {
  color: #0a101a;
  cursor: pointer;
  font-weight: bold;
  padding: 0;
}
@media (max-width: 600px) {
  .accordionForm .content .label {
    font-size: 0.875rem;
  }
}
.accordionForm .content .description {
  margin: 0;
  margin-top: 0.5rem;
  color: rgba(10, 16, 26, 0.7);
  line-height: 1.25;
}
.accordionForm .content .description * {
  margin: 0;
}
.accordionForm .content .helper {
  display: block;
  text-decoration: underline;
  line-height: 1.5;
}
.accordionForm .content .rightElement {
  color: #000083;
  font-weight: bold;
  padding: 1rem 0;
  padding-right: 1rem;
}
.accordionForm.collapsed > .content {
  background-color: #e9edf6;
  border-color: #d1d6e0;
  border-bottom-style: dashed;
}
.accordionForm.collapsed > .dropdown {
  border: 1px solid #000083;
  border-top-left-radius: 0.125rem;
  border-top-right-radius: 0.125rem;
  border-top-style: dashed;
}
.accordionForm.open > .content {
  background-color: #fff;
  border-color: #000083;
}
.accordionForm.open > .content > .main .icon:not(.collapsed) {
  background-color: #fff;
  border-left: 1px solid #000083;
  border-bottom: 1px solid #000083;
}
.accordionForm.open > .content > .main .icon:not(.collapsed) i:not(.collapsed) {
  transform: rotate(180deg);
  color: #000083;
}
.accordionForm.open > .dropdown {
  display: block;
  margin-top: -1px;
}
.accordionForm.open.withChildren > .content {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  border-bottom: 0;
}
.accordionForm.open::before {
  content: "";
  display: block;
  position: absolute;
  width: 0.5rem;
  height: calc(100% - 2px);
  background-color: #94d232;
  left: 1px;
  top: 1px;
  border-top-left-radius: 0.125rem;
  border-bottom-left-radius: 0.125rem;
}
.accordionForm.open.pricing:not(.checked)::before {
  content: none;
}
.accordionForm.open.pricing:not(.checked) > .content {
  background-color: #e9edf6;
  border: 1px solid #d1d6e0;
}
.accordionForm.open.pricing:not(.checked) > .content > .main .icon:not(.collapsed) {
  border: 2px solid #000083;
  margin-top: -1px;
  margin-right: -1px;
}
.accordionForm.open.pricing:not(.checked) > .dropdown {
  border: 1px solid #d1d6e0;
  border-top-color: #fff;
}
.accordionForm .radioInput {
  align-items: center;
  background-color: #fff;
  border: 2px solid #000083;
  border-radius: 50%;
  box-sizing: border-box;
  cursor: pointer;
  display: flex;
  justify-content: center;
  height: 16px;
  width: 16px;
}
.accordionForm .radioInput input[type=radio] {
  opacity: 0;
  width: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
}
.accordionForm .radioInputSelected {
  height: 8px;
  width: 8px;
  border-radius: 50%;
  background-color: #000083;
}

.logo {
  box-sizing: content-box;
  max-height: 25px;
  margin-inline: auto 1rem;
  padding-block: 1.5rem;
}
.logo.condensedLogo {
  padding: 0;
  align-self: center;
}
@media (max-width: 840px) {
  .logo {
    display: none;
  }
}