@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.bookTeaser {
  align-items: center;
  display: grid;
  height: 350px;
  grid-template-rows: 70% auto auto;
  margin: 0 1.5rem;
}
.bookTeaser.portraitThumbnailWrapper, .bookTeaser.squareThumbnailWrapper {
  max-width: 160px;
}
.bookTeaser.landscapeThumbnailWrapper {
  max-width: 223px;
}
.bookTeaser .image {
  align-self: flex-end;
}
.bookTeaser .image div {
  position: relative;
}
.bookTeaser .image div::before {
  content: "";
  display: block;
  position: absolute;
  width: 10px;
  height: 100%;
  background-image: linear-gradient(90deg, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0) 10%, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.2) 80%, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
  opacity: 0.25;
  border-right: 1px solid #717c8f;
}
.bookTeaser .calendarImage {
  align-self: flex-end;
}
.bookTeaser .calendarImage div {
  position: relative;
}
.bookTeaser .calendarImage div .overlay {
  position: absolute;
  width: 100%;
  pointer-events: none;
}
.bookTeaser .productImage {
  box-shadow: 8px 10px 16px 0 rgba(0, 0, 0, 0.2);
  display: block;
  transition: 0.3s;
}
.bookTeaser .productImage:hover {
  box-shadow: 8px 10px 16px 0 rgba(0, 0, 0, 0.35);
}
.bookTeaser .portraitImage,
.bookTeaser .squareImage {
  max-width: 10rem;
  height: auto;
}
.bookTeaser .landscapeImage {
  width: auto;
  height: 10rem;
}
.bookTeaser .title {
  align-self: start;
  font-family: "museo-sans", sans-serif;
  font-weight: 700;
  line-height: 1.25;
  word-wrap: break-word;
  hyphens: auto;
  margin-top: 1.25rem;
}
.bookTeaser .author {
  align-self: start;
  font-family: "interface", sans-serif;
  word-wrap: break-word;
  hyphens: auto;
}
.bookTeaser .author i {
  margin-right: 0.5rem;
  vertical-align: middle;
}