@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  margin-bottom: 2rem;
}
@media (min-width: 840px) {
  .container {
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 1024px) {
  .container {
    margin-bottom: 2.5rem;
  }
}

.title {
  font-weight: 300;
  font-size: 1.5rem;
  margin: 0 0 1.5rem;
}
@media (min-width: 840px) {
  .title {
    font-size: 2rem;
  }
}

.description {
  font-weight: 300;
  font-size: 1.25rem;
  color: rgba(10, 16, 26, 0.6);
  margin: 0;
}
@media (min-width: 840px) {
  .description {
    font-size: 1.25rem;
  }
}

.informationContainerDevPortal {
  background-color: #f2f4fa;
  border: 1px solid #d1d6e0;
  border-radius: 2px;
  padding: 1rem;
  display: flex;
  gap: 1rem;
  align-items: center;
}
@media (min-width: 600px) {
  .informationContainerDevPortal {
    width: 60%;
  }
}
@media (min-width: 840px) {
  .informationContainerDevPortal {
    flex-direction: column;
  }
}
@media (min-width: 1024px) {
  .informationContainerDevPortal {
    flex-direction: row;
    gap: 1.5rem;
    width: auto;
    max-width: 60%;
  }
}

.informationContainer {
  background-color: #f2f4fa;
  border: 1px solid #d1d6e0;
  border-radius: 2px;
  padding: 1rem;
  display: flex;
  gap: 1rem;
  align-items: center;
  position: relative;
}
@media (min-width: 840px) {
  .informationContainer {
    max-width: 50%;
    min-width: 336px;
  }
}
@media (min-width: 1024px) {
  .informationContainer {
    flex-direction: row;
    gap: 1.5rem;
    width: auto;
    max-width: 60%;
    min-width: 425px;
  }
}
.informationContainer p {
  margin: 0;
  margin-bottom: 0.5rem;
}

.information {
  font-size: 1rem;
  font-weight: 300;
  margin: 0;
  flex: 1;
  max-width: 340px;
}
.information a {
  text-decoration: underline;
}
@media (min-width: 1024px) {
  .information {
    width: 281px;
    font-size: 1.25rem;
  }
  .information.devPortal {
    width: 220px;
  }
  .developerPortal .information {
    width: 140px;
    font-size: 1rem;
  }
}

.imageContainer {
  width: 56px;
  height: 60px;
}
.imageContainer img {
  scale: 1.3;
  translate: 0 5px;
}
@media (min-width: 600px) {
  .imageContainer {
    width: 69px;
    height: 69px;
  }
  .imageContainer img {
    scale: 1.2;
    translate: 0;
  }
}
@media (min-width: 840px) {
  .imageContainer {
    width: 86px;
    height: 80px;
  }
  .imageContainer img {
    scale: 1.1;
  }
}
@media (min-width: 1024px) {
  .imageContainer {
    width: 109px;
    height: 109px;
  }
  .imageContainer img {
    translate: 0 7px;
  }
}

.downloadButton {
  display: block;
  margin-top: 0.5rem;
}