@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.selector {
  display: flex;
  flex-direction: column;
}

.option {
  position: relative;
  border: 1px solid #d1d6e0;
  display: flex;
  flex-direction: column;
  border-radius: 2px;
  cursor: pointer;
}
.option.selected {
  border-color: #000083;
}
.option.disabled {
  cursor: auto;
}
.option:hover {
  border-color: #000083;
}
.option.disabled:hover {
  border-color: #d1d6e0;
}
.option.nonInteractive {
  pointer-events: none;
}

.imageContainer {
  position: relative;
}

.disabledFog::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.6);
}

.disabledInfo {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.disabledInfo * {
  background-color: #fff;
  border: 1px solid #d1d6e0;
  border-radius: 0.125rem;
}
.disabledInfo span {
  color: rgba(10, 16, 26, 0.7);
  line-height: 1.25;
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
  font-family: "museo-sans", sans-serif;
  text-align: center;
}
@media (min-width: 1024px) {
  .disabledInfo span {
    font-size: 1rem;
  }
}

.image {
  height: 100%;
  width: 100%;
}

.optionContentContainer {
  position: relative;
  display: flex;
  padding: 0.5rem 0.5rem 1rem;
  gap: 0.5rem;
}

.radio {
  border: 2px solid #000083;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  position: relative;
  align-self: flex-start;
  margin-top: 10px;
}
.radio.selected::before {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  background-color: #000083;
  translate: -50% -50%;
  left: 50%;
  top: 50%;
  border-radius: 50%;
}

.radioGroup {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  flex: 1;
  margin-bottom: 2rem;
}
@media (min-width: 600px) {
  .radioGroup {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 840px) {
  .radioGroup {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1440px) {
  .radioGroup {
    grid-template-columns: repeat(4, 1fr);
  }
}

.rowLayoutItem {
  height: 100%;
}
.rowLayoutItem:hover {
  border-color: #d1d6e0;
}

.label {
  flex: 1;
  margin: 10px 0 0;
  font-weight: 400;
  font-size: 0.875rem;
  align-self: flex-start;
}
@media (min-width: 1024px) {
  .label {
    font-size: 1rem;
  }
}

.subSectionTitle {
  margin: 0 0 1rem 0;
  font-weight: 700;
  font-size: 1rem;
}

.description {
  font-size: 12px;
  font-family: "museo-sans", sans-serif;
  font-weight: 300;
  margin-top: 6px;
}

:global(.isDeveloperPortalVersion) .distroIcon {
  display: none;
}