@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.imageBannerWrapper {
  position: relative;
  min-height: 320px;
  overflow: hidden;
}
@media (max-width: 600px) {
  .imageBannerWrapper {
    min-height: 360px;
  }
}
.imageBannerWrapper > a,
.imageBannerWrapper picture,
.imageBannerWrapper img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.contentContainer {
  position: relative;
}
.contentContainer h2 {
  margin-bottom: 0.5rem;
}