@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container {
  border-radius: 2px;
  border: 1px solid #000083;
  background: #fff;
  padding: 1rem;
}
.container.naked {
  border: none;
  border-bottom: 1px solid #8d97a8;
  padding-bottom: 0.65rem;
}
@media (min-width: 600px) {
  .container {
    padding: 1.5rem;
  }
}

.currencySelectorWrapper {
  margin-left: auto;
  min-width: 112px;
}

.currencyConversionInput {
  color: rgba(10, 16, 26, 0.6);
}

.currencyConversion {
  font-size: 0.875rem;
  text-align: left;
}
@media (min-width: 600px) {
  .currencyConversion {
    font-size: 1rem;
  }
}

.title {
  margin: 0 0 0.5rem;
  font-weight: 700;
}

.table {
  width: 100%;
  border-spacing: 0;
  border-collapse: collapse;
}

.cell {
  text-align: right;
  font-family: "museo-sans", sans-serif;
  font-weight: 300;
  font-size: 0.875rem;
  padding: 0.42rem 0 0.42rem 0.5rem;
}
.cell:first-child {
  text-align: left;
  padding-left: 1rem;
  white-space: normal;
}
.cell:last-child {
  padding-right: 1rem;
}
@media (min-width: 600px) {
  .cell {
    font-size: 1rem;
  }
}

.fitContent {
  padding-left: 1rem;
  width: 1px;
}

@media (min-width: 600px) {
  .headerCurrencies {
    display: none;
  }
}

.cellHeader {
  padding-bottom: 1rem;
  font-weight: 400;
  text-align: right;
  font-size: 0.875rem;
}
@media (min-width: 600px) {
  .cellHeader {
    font-size: 1rem;
  }
}

@media (max-width: 600px) {
  .cellHeaderCurrency {
    display: none;
  }
}

.cellSecondHeader {
  padding-bottom: 1.5rem;
  min-width: 40px;
}

.defaultCurrencyHeaderCell {
  display: none;
}
@media (min-width: 600px) {
  .defaultCurrencyHeaderCell {
    display: table-cell;
  }
}

.paddingRight {
  padding-right: 1rem;
}

.paddingLeft {
  padding-left: 1rem;
}

.nameColumn {
  flex: 1;
}

.firstRowCell {
  padding-top: 1rem;
  border-top: 1px solid #8d97a8;
}

.summaryRow {
  background-color: #94d232;
  white-space: nowrap;
}
.naked .summaryRow {
  background-color: transparent;
}

.summaryCell {
  padding: 1rem 0 1rem 0.5rem;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
}
.naked .summaryCell {
  padding: 0.5rem 0 0.5rem 1rem;
}

.summaryCellFirst {
  font-size: 0.75rem;
}

.summaryCellFlex {
  position: relative;
}

.summaryAmountAbs {
  position: absolute;
  height: 100%;
  top: 0;
  display: flex;
  align-items: center;
  right: 1rem;
  font-size: 1rem;
  font-weight: 600;
}
@media (max-width: 1024px) {
  .summaryAmountAbs {
    font-size: 0.875rem;
  }
}
@media (max-width: 480px) {
  .summaryAmountAbs {
    font-size: 0.75rem;
  }
}

.summaryCurrency {
  display: none;
}
@media (min-width: 600px) {
  .summaryCurrency {
    display: inline;
    margin-left: 0.5rem;
  }
}

.separator {
  height: 0.25rem;
}

.disabled {
  opacity: 0.5;
  pointer-events: none;
}
.disabled.naked {
  opacity: 1;
}