@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container {
  margin-top: 1rem;
  background-color: #e9edf6;
  padding: 1rem;
}
@media (min-width: 840px) {
  .container {
    padding: 2rem;
  }
}

.bulkDiscountRangesTable {
  display: flex;
  flex-direction: column;
  border: 1px solid #000083;
  background-color: #fff;
  padding: 0.5rem 1rem;
  border-radius: 2px;
}
@media (min-width: 600px) {
  .bulkDiscountRangesTable {
    padding: 1rem 1.5rem;
  }
}

.description {
  font-family: "museo-sans", sans-serif;
  font-weight: 300;
  margin-bottom: 1.5rem;
  font-size: 0.875rem;
  display: block;
}
@media (min-width: 840px) {
  .description {
    font-size: 1rem;
  }
}

.bulkDiscountRangesTitle {
  font-weight: 900;
  margin: 0 0 0.5rem;
}

.estimateTitle {
  padding-bottom: 0.5rem;
  border-bottom: 1px solid #8d97a8;
  font-size: 0.875rem;
  font-weight: 600;
}
@media (min-width: 840px) {
  .estimateTitle {
    font-size: 1rem;
  }
}

.tableRow {
  display: flex;
  padding: 0.5rem 0;
}
.tableRow:first-child {
  white-space: nowrap;
}

.tableRecord {
  display: flex;
  flex: 1;
  font-family: "museo-sans", sans-serif;
  font-weight: 300;
  align-items: center;
  justify-content: center;
  font-size: 0.875rem;
}
@media (min-width: 840px) {
  .tableRecord {
    font-size: 1rem;
  }
}

.header {
  font-weight: 600;
  text-align: center;
  white-space: nowrap;
}

.icon {
  margin-left: 0.5rem;
}

.splitter {
  border-bottom: 1px solid #8d97a8;
}

.firstDataRow {
  margin-top: 0.5rem;
}

.numberOfCopiesWrapper {
  margin-top: 1rem;
}
@media (min-width: 480px) {
  .numberOfCopiesWrapper {
    max-width: 264px;
  }
}

.selectContainer {
  margin-bottom: 1rem;
}

.summaryContainer {
  border-top: 1px solid #8d97a8;
}

.summaryItem {
  display: flex;
  justify-content: space-between;
  font-family: "museo-sans", sans-serif;
  font-weight: 400;
  margin: 0 0 0.5rem;
  font-size: 0.875rem;
  line-height: 1.5;
}
@media (min-width: 840px) {
  .summaryItem {
    font-size: 1rem;
  }
}
.summaryItem:first-child {
  margin-top: 1rem;
}
.summaryItem:last-child {
  margin: 0;
}
.summaryItem:only-child {
  margin-top: 1rem;
}

.money {
  font-weight: 300;
}

.subtotalExcludingFees {
  font-weight: 600;
}

.subtotalExcludingFeesDescription {
  font-size: 0.75rem;
  color: #717c8f;
  font-weight: 300;
}

.tooltipIconWrapper {
  display: none;
}
@media (min-width: 480px) {
  .tooltipIconWrapper {
    display: block;
  }
}

.tooltip {
  padding: 0.25rem;
}

.tooltipWrapper {
  width: 240px;
  white-space: pre-line;
}

.tooltipHeader {
  font-family: "interface", sans-serif;
  font-size: 0.875rem;
  margin: 0 0 0.5rem;
}

.tooltipDescription {
  font-family: "interface", sans-serif;
  font-size: 0.875rem;
  font-weight: 300;
}

.tooltipLink {
  text-decoration: underline;
}