@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.mainBannerWrapper {
  position: relative;
}
.mainBannerWrapper img,
.mainBannerWrapper video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 600px) {
  .mainBannerWrapper video {
    display: none;
  }
}

.mainBannerImageWrapper {
  position: relative;
}
.mainBannerImageWrapper.shortHeight {
  min-height: 480px;
}
@media (max-width: 840px) {
  .mainBannerImageWrapper.shortHeight {
    min-height: 360px;
  }
}
@media (max-width: 600px) {
  .mainBannerImageWrapper.shortHeight {
    min-height: 141px;
  }
}
.mainBannerImageWrapper.mediumHeight {
  min-height: 720px;
}
@media (max-width: 840px) {
  .mainBannerImageWrapper.mediumHeight {
    min-height: 640px;
  }
}
@media (max-width: 600px) {
  .mainBannerImageWrapper.mediumHeight {
    min-height: 540px;
  }
}
.mainBannerImageWrapper.tallHeight {
  min-height: 960px;
}
@media (max-width: 840px) {
  .mainBannerImageWrapper.tallHeight {
    min-height: 880px;
  }
}
@media (max-width: 600px) {
  .mainBannerImageWrapper.tallHeight {
    min-height: 720px;
  }
}

.mainBannerContentContainer {
  position: relative;
}
.mainBannerContentContainer.shortContainer {
  padding-top: 3.5rem;
}
@media (max-width: 840px) {
  .mainBannerContentContainer.shortContainer {
    padding-top: 3rem;
  }
}
@media (max-width: 600px) {
  .mainBannerContentContainer.shortContainer {
    padding-top: 2rem;
  }
}
.mainBannerContentContainer.mediumContainer {
  padding-top: 6rem;
}
@media (max-width: 840px) {
  .mainBannerContentContainer.mediumContainer {
    padding-top: 5rem;
  }
}
@media (max-width: 600px) {
  .mainBannerContentContainer.mediumContainer {
    padding-top: 3rem;
  }
}
.mainBannerContentContainer.tallContainer {
  padding-top: 9rem;
}
@media (max-width: 840px) {
  .mainBannerContentContainer.tallContainer {
    padding-top: 7.75rem;
  }
}
@media (max-width: 600px) {
  .mainBannerContentContainer.tallContainer {
    padding-top: 3.5rem;
  }
}
.mainBannerContentContainer.noTopPadding {
  padding-top: 0;
}

.buttonWrapper {
  margin-top: 2rem;
}
@media (max-width: 1024px) {
  .buttonWrapper {
    margin-top: 1.5rem;
  }
}