@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.selectAndButton {
  display: flex;
  width: 100%;
}

.labelAndHelp {
  position: relative;
  display: flex;
  width: 100%;
}
.labelAndHelp .label {
  font-size: 0.75rem;
}

.tooltip {
  position: absolute;
  bottom: 0;
  right: 0;
}

.helpText {
  font-family: "museo-sans", sans-serif;
  font-size: 0.625rem;
  color: rgba(10, 16, 26, 0.6);
  text-transform: uppercase;
  margin-left: auto;
  font-weight: 300;
}