@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container {
  margin-bottom: 2.5rem;
}

.selectorsContainer {
  display: flex;
  gap: 1rem;
  padding-top: 0.5rem;
}
@media (max-width: 600px) {
  .selectorsContainer {
    flex-direction: column;
    gap: 0.8rem;
  }
}

.pageCountContainer {
  display: flex;
  flex-direction: column;
  width: 187px;
}

.inputDropdownContainer {
  display: flex;
  flex: 1;
}

.inputDropdownWrapper {
  margin-top: 15.5px;
}
@media (min-width: 600px) {
  .inputDropdownWrapper.withBottomMargin {
    margin-bottom: 30px;
  }
}

.pageCountWrapper {
  width: 187px;
}