@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.button {
  border: 1px solid transparent;
  border-radius: 100px;
  transition: all 100ms ease-out;
  position: relative;
}
.button:hover {
  cursor: pointer;
}
.button:disabled {
  cursor: not-allowed;
}
.button .text {
  vertical-align: text-top;
}
.button .text.hidden {
  visibility: hidden;
}
.button .icon {
  vertical-align: middle;
  transition: color 100ms ease-out;
}
.button.large {
  font-family: "museo-sans", sans-serif;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1.375rem;
  padding: 0.75rem 1.75rem;
}
.button.large.iconLeft {
  padding-left: 1.25rem;
}
.button.large.iconLeft .icon {
  margin-right: 0.625rem;
}
.button.large.iconRight {
  padding-right: 1.25rem;
}
.button.large.iconRight .icon {
  margin-left: 0.625rem;
}
.button.large .icon {
  font-size: 1.5rem;
}
.button.medium {
  font-family: "museo-sans", sans-serif;
  font-size: 0.875rem;
  font-weight: 700;
  line-height: 1.125rem;
  padding: 0.625rem 1.5rem;
}
.button.medium.iconLeft {
  padding-left: 1rem;
}
.button.medium.iconLeft .icon {
  margin-right: 0.5rem;
}
.button.medium.iconRight {
  padding-right: 1rem;
}
.button.medium.iconRight .icon {
  margin-left: 0.5rem;
}
.button.medium .icon {
  font-size: 1.25rem;
}
.button.small {
  font-family: "museo-sans", sans-serif;
  font-size: 0.75rem;
  font-weight: 700;
  line-height: 1rem;
  padding: 0.5rem 1.25rem;
}
.button.small.iconLeft {
  padding-left: 0.75rem;
}
.button.small.iconLeft .icon {
  margin-right: 0.375rem;
}
.button.small.iconRight {
  padding-right: 0.75rem;
}
.button.small.iconRight .icon {
  margin-left: 0.375rem;
}
.button.small .icon {
  font-size: 1rem;
}
.button.primary {
  color: #fbfbfc;
  background-color: #303a4d;
}
.button.primary:hover:not(:disabled) {
  background-color: #445461;
}
.button.primary:hover:not(:disabled):active {
  background-color: #192229;
}
.button.primary:disabled {
  background-color: #e4e5e6;
}
.button.primary .icon {
  color: #fbfbfc;
}
.button.secondary {
  color: #303a4d;
  background-color: rgba(0, 0, 0, 0.04);
}
.button.secondary:hover:not(:disabled) {
  color: #192229;
  background-color: rgba(0, 0, 0, 0.06);
}
.button.secondary:hover:not(:disabled):active {
  color: #303a4d;
  background-color: rgba(0, 0, 0, 0.1);
}
.button.secondary:hover:not(:disabled):active .icon {
  color: #303a4d;
}
.button.secondary:hover:not(:disabled) .icon {
  color: #192229;
}
.button.secondary:disabled {
  color: #e4e5e6;
}
.button.secondary:disabled .icon {
  color: #e4e5e6;
}
.button.secondary .icon {
  color: #303a4d;
}
.button.outline {
  color: #303a4d;
  background-color: transparent;
  border-color: #303a4d;
}
.button.outline:hover:not(:disabled) {
  color: #192229;
  background-color: rgba(0, 0, 0, 0.04);
  border-color: #192229;
}
.button.outline:hover:not(:disabled) .icon {
  color: #192229;
}
.button.outline:hover:not(:disabled):active {
  color: #303a4d;
  background-color: rgba(0, 0, 0, 0.1);
  border-color: #303a4d;
}
.button.outline:hover:not(:disabled):active .icon {
  color: #303a4d;
}
.button.outline:disabled {
  color: #e4e5e6;
  border-color: #e4e5e6;
}
.button.outline:disabled .icon {
  color: #e4e5e6;
}
.button.outline .icon {
  color: #303a4d;
}
.button.destructive {
  color: #fbfbfc;
  background-color: #e84855;
}
.button.destructive:hover:not(:disabled) {
  background-color: #f3727d;
}
.button.destructive:hover:not(:disabled):active {
  background-color: #bf3b46;
}
.button.destructive:disabled {
  background-color: #faebeb;
}
.button.destructive .icon {
  color: #fbfbfc;
}
.button.contrast {
  color: #303a4d;
  background-color: #fff;
}
.button.contrast:hover:not(:disabled) {
  color: #fbfbfc;
  background-color: #303a4d;
}
.button.contrast:hover:not(:disabled):active {
  background-color: #192229;
}
.button.contrast:hover:not(:disabled) .icon {
  color: #fbfbfc;
}
.button.contrast:disabled {
  color: #e4e5e6;
}
.button.contrast .icon {
  color: #303a4d;
}
.button.ghost {
  color: #303a4d;
  background-color: transparent;
}
.button.ghost:hover:not(:disabled) {
  background-color: rgba(0, 0, 0, 0.04);
}
.button.ghost:hover:not(:disabled):active {
  background-color: rgba(0, 0, 0, 0.1);
}
.button.ghost:disabled {
  color: #e4e5e6;
}
.button.ghost:disabled .icon {
  color: #e4e5e6;
}
.button.ghost .icon {
  color: #303a4d;
}

.spinnerContainer {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.spinner {
  animation: spin 1s linear infinite;
}
.spinner.primary path, .spinner.destructive path {
  fill: #fbfbfc;
}
.spinner.secondary path, .spinner.outline path, .spinner.contrast path, .spinner.ghost path {
  fill: #e4e5e6;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}