@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.button {
  border-radius: 100%;
  border: 3px solid transparent;
  background-color: rgba(0, 0, 0, 0.04);
  padding: 0.5rem;
  position: relative;
  transition: background-color 100ms;
}
.button:hover:not(:disabled) {
  cursor: pointer;
}
.button:disabled {
  cursor: not-allowed;
  background-color: rgba(0, 0, 0, 0.04);
}
.button:disabled .icon {
  color: #e4e5e6;
}
.button .icon {
  transition: color 100ms;
  color: var(--color);
  position: absolute;
  left: 50%;
  top: 50%;
  translate: -50% -50%;
}
.button .badge {
  position: absolute;
  top: -7px;
  right: -7px;
}
.button.large {
  width: 40px;
  height: 40px;
}
.button.medium {
  width: 32px;
  height: 32px;
}
.button.small {
  width: 20px;
  height: 20px;
}
.button.default:hover:not(:disabled) {
  background-color: rgba(0, 0, 0, 0.06);
}
.button.default:hover:not(:disabled):active {
  background-color: rgba(0, 0, 0, 0.1);
}
.button.default.selected {
  background-color: rgba(0, 0, 0, 0.06);
}
.button.outline {
  border-color: #f5f4f2;
  background-color: transparent;
}
.button.outline:hover:not(:disabled):active {
  background-color: #fafaf9;
}
.button.outline:hover:not(:disabled) .icon {
  color: #192229;
}
.button.ghost {
  background-color: transparent;
}
.button.ghost:before {
  content: "";
  position: absolute;
  inset: -3px;
  border-radius: 100%;
  background-color: black;
  transition: opacity 0.1s;
  opacity: 0%;
}
.button.ghost:hover:not(:disabled) {
  background-color: rgba(0, 0, 0, 0.04);
}
.button.ghost:hover:not(:disabled):active:before {
  opacity: 10%;
}
.button.ghost.selected {
  background-color: rgba(0, 0, 0, 0.04);
}
.button.shiftLeft:hover:not(:disabled) {
  transition: translate 150ms;
  translate: -3px;
}
.button.shiftRight:hover:not(:disabled) {
  transition: translate 150ms;
  translate: 3px;
}