@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.toolButton {
  -webkit-appearance: none;
  -moz-appearance: none;
  color: rgba(10, 16, 26, 0.6);
  display: flex;
  align-items: center;
  gap: 9px;
  border: 1px solid transparent;
  padding: 0 7px;
  height: 32px;
  transition: 0.3s;
  background-color: transparent;
}
.toolButton *,
.toolButton *::before,
.toolButton *::after {
  box-sizing: border-box;
}
.toolButton:focus {
  box-shadow: none;
  transition: none;
}
.toolButton:hover {
  color: #000083;
  background-color: #e9edf6;
  border-color: #aeb6c2;
}
.toolButton:active, .toolButton.active {
  color: #000083;
  background-color: transparent;
  border-color: #000083;
}
.toolButton.compact {
  padding: 2px 3px;
  height: 24px;
}
.toolButton:disabled {
  opacity: 0.5;
}

.addOnContainer {
  all: initial;
}
.addOnContainer:focus, .addOnContainer:hover, .addOnContainer:active, .addOnContainer.active {
  all: initial;
}
.addOnContainer > *:first-child {
  cursor: pointer;
}