@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.title {
  display: flex;
  color: #000083;
  align-items: center;
  border-bottom: 2px solid #000083;
  padding: 0.625rem 0;
  font-family: "museo-sans", sans-serif;
  font-size: 0.75rem;
  font-weight: 600;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 600px) {
  .title {
    font-size: 1.25rem;
    padding: 1rem 0;
  }
}

.contentContainer {
  background-color: #e9edf6;
  padding: 1rem;
}
@media (min-width: 840px) {
  .contentContainer {
    padding: 2rem;
  }
}
.contentContainer p {
  font-family: "museo-sans", sans-serif;
  font-weight: 300;
}

p.minimumListPrice {
  font-family: "museo-sans", sans-serif;
  font-size: 0.875rem;
  margin: 0 0 0.5rem;
  font-weight: 400;
  line-height: 1.5;
}
@media (min-width: 600px) {
  p.minimumListPrice {
    font-size: 1rem;
  }
}

.listPriceInput,
.inputDropdown {
  margin-bottom: 1.5rem;
}
@media (min-width: 840px) {
  .listPriceInput,
  .inputDropdown {
    width: 264px;
  }
}

.inputDropdown {
  margin-top: 1.5rem;
}

.price {
  font-weight: bold;
}

.revenueTableWrapper {
  margin-bottom: 1.5rem;
}

.globalDistroContainer {
  padding: 1rem;
  border-radius: 2px;
  border: 1px solid #000083;
  background: #fff;
}
@media (min-width: 840px) {
  .globalDistroContainer {
    padding: 1.5rem;
  }
}
.globalDistroContainer.disabled {
  opacity: 0.5;
  pointer-events: none;
}

.globalDistroTitle {
  border-bottom: 1px solid #8d97a8;
  padding-bottom: 1rem;
  margin: 0 0 0.5rem 0;
  font-weight: 700;
}

.toggleContainer {
  display: flex;
  justify-content: flex-end;
}