@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.dot {
  height: 8px;
  width: 8px;
  border-radius: 100%;
  background-color: #e84855;
}

.number {
  font-family: "museo-sans", sans-serif;
  font-size: 0.75rem;
  font-weight: 700;
  line-height: 1rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.number.cart {
  min-width: 16px;
  min-height: 16px;
  padding: 0.125rem;
  font-size: 10px;
  line-height: 12px;
  background-color: #303a4d;
  color: #fbfbfc;
  border-radius: 100px;
}
.number.bookBadge {
  height: 28px;
  width: 28px;
  background-color: #fff;
  border: 3px solid #f5f4f2;
  color: #768189;
  border-radius: 100%;
}

.text {
  font-family: "museo-sans", sans-serif;
  font-size: 0.75rem;
  font-weight: 700;
  line-height: 1rem;
  text-transform: uppercase;
  border-radius: 1000px;
  padding: 0.125rem 0.75rem;
  border: 1px solid transparent;
  border-radius: 1000px;
}
.text.info {
  color: #6056e8;
  background-color: #e8e7f4;
}
.text.success {
  color: #3d856a;
  background-color: #def7f0;
}
.text.error {
  color: #e84855;
  background-color: #faeceb;
}
.text.neutral {
  color: #768189;
  background-color: rgba(0, 0, 0, 0.04);
}
.text.muted {
  color: #a7adb2;
  background-color: rgba(0, 0, 0, 0.02);
}
.text.outlined {
  color: #a7adb2;
  border-color: #a7adb2;
}