@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.loadingContainer {
  grid-column: 1/-1;
  height: 200px;
  display: flex;
  align-items: center;
}

.container {
  --panel-width-xl: 533px;
  --panel-width-l: 320px;
  gap: 2rem;
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
}
@media (min-width: 1024px) {
  .container {
    grid-template-columns: 1fr var(--panel-width-l);
  }
}
@media (min-width: 1440px) {
  .container {
    grid-template-columns: 1fr var(--panel-width-xl);
  }
}

.specsPanelContainer {
  width: 100%;
  position: fixed;
  z-index: 2;
  max-height: 100vh;
  bottom: 0;
  left: 0;
}
@media (min-width: 1024px) {
  .specsPanelContainer {
    position: sticky;
    width: var(--panel-width-l);
    z-index: auto;
    top: 1rem;
    bottom: auto;
    left: auto;
  }
}
@media (min-width: 1440px) {
  .specsPanelContainer {
    width: var(--panel-width-xl);
  }
}

.configuratorHeader {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2.5rem;
  flex-direction: column;
  gap: 1rem;
}
@media (min-width: 600px) {
  .configuratorHeader {
    flex-direction: row;
    gap: 0;
  }
}
.configuratorHeader h2 {
  font-size: 1.25rem;
}
@media (min-width: 840px) {
  .configuratorHeader h2 {
    font-size: 1.5rem;
  }
}

.globalDistroHint {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-family: "museo-sans", sans-serif;
  font-size: 0.875rem;
}
@media (min-width: 600px) {
  .globalDistroHint {
    font-size: 1rem;
  }
}

.configuratorLayoutRow {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  flex: 1;
  margin-bottom: 2rem;
}
@media (min-width: 600px) {
  .configuratorLayoutRow {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1440px) {
  .configuratorLayoutRow {
    grid-template-columns: repeat(4, 1fr);
  }
}