@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.bookRadioGroup {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -0.625rem;
}
@media (max-width: 480px) {
  .bookRadioGroup {
    justify-content: center;
  }
}
.bookRadioGroup > * > * {
  margin: 1.375rem 0.625rem;
}