@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.gutter {
  margin-bottom: 1rem;
}

.grid {
  grid-gap: 1.25rem;
  justify-content: flex-start;
}

.title {
  margin: 0;
  line-height: 1.1;
  font-size: 1.25rem;
}
@media (max-width: 480px) {
  .title {
    font-size: 1rem;
  }
}
.title_noOrder {
  composes: h2 from global;
  margin: 0;
  line-height: 1.17;
}
.titleBigger {
  font-size: 1.5rem;
}
@media (max-width: 480px) {
  .titleBigger {
    font-size: 1.25rem;
  }
}

.description {
  margin: 0;
  color: rgba(10, 16, 26, 0.6);
  font-weight: 300;
  line-height: 1.5;
  font-size: 1rem;
}
@media (max-width: 480px) {
  .description {
    font-size: 0.875rem;
  }
}
.description * {
  margin: 0;
}
.description_noOrder {
  font-size: 1.25rem;
  line-height: 1.4;
}
@media (max-width: 1024px) {
  .description_noOrder {
    font-size: 1rem;
  }
}
.descriptionBigger {
  font-size: 1.25rem;
}
@media (max-width: 480px) {
  .descriptionBigger {
    font-size: 1rem;
  }
}

.orderBox {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  height: 56px;
  overflow: hidden;
  margin-bottom: 1rem;
}
.orderBox .orderBar {
  width: calc(100% - 2.5rem);
  background-color: #25334d;
  height: 2px;
}
@media (max-width: 480px) {
  .orderBox .orderBar {
    width: 100%;
  }
}
.orderBox > .orderBar:first-child {
  display: none;
}
@media (max-width: 480px) {
  .orderBox > .orderBar:first-child {
    display: flex;
  }
}
.orderBox .orderNumber {
  border-radius: 50%;
  width: 1.5rem;
  height: 1.5rem;
  line-height: 1rem;
  margin-right: 1rem;
  box-sizing: content-box;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-family: "museo-sans", sans-serif;
  font-weight: 900;
  color: #fff;
  background-color: #25334d;
}
@media (max-width: 480px) {
  .orderBox .orderNumber {
    width: 3.7rem;
    margin-left: 1rem;
  }
}

.vertical {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}