@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.pageHeader {
  padding-top: 3.75rem;
}
@media (max-width: 840px) {
  .pageHeader {
    padding-top: 3rem;
  }
}
@media (max-width: 600px) {
  .pageHeader {
    padding-top: 2.5rem;
  }
}
@media (max-width: 480px) {
  .pageHeader {
    padding-top: 2rem;
  }
}

.title {
  font-size: 2rem;
  margin: 0;
  margin-bottom: 0.5rem;
  font-weight: 300;
}
@media (max-width: 600px) {
  .title {
    font-size: 1.5rem;
  }
}

.subtitle {
  margin: 0;
  font-weight: 300;
  font-family: "museo-sans", sans-serif;
  color: rgba(10, 16, 26, 0.6);
  font-size: 1.25rem;
  line-height: 1.25;
}
@media (max-width: 480px) {
  .subtitle {
    font-size: 1rem;
  }
}