@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.button {
  display: inline-block;
  border: none;
  border-radius: 0.125rem;
  position: relative;
  text-align: center;
  line-height: 1;
  font-family: "museo-sans", sans-serif;
  background-color: var(--bg-color);
}
.button:focus {
  outline: none;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
}
.button:hover, .button:focus {
  cursor: pointer;
  transition: 0.3s;
  text-decoration: none;
}
.button:disabled, .button[aria-disabled=true] {
  cursor: default;
  pointer-events: none;
}
.button.isVerticallyStretched {
  height: 100%;
}
.button.primary {
  --bg-color: #000083;
  color: #fff;
}
.button.primary:hover, .button.primary:focus {
  background-color: #2424b5;
}
.button.primary:disabled:not([data-progress=true]), .button.primary[aria-disabled=true]:not([data-progress=true]) {
  opacity: 0.6;
}
.button.primary:disabled:not([data-progress=true]):hover, .button.primary[aria-disabled=true]:not([data-progress=true]):hover {
  background-color: #000083;
}
.button.secondary {
  --bg-color: #22b5f0;
  color: #000083;
}
.button.secondary:hover, .button.secondary:focus {
  background-color: #3dc8ff;
}
.button.secondary:disabled:not([data-progress=true]), .button.secondary[aria-disabled=true]:not([data-progress=true]) {
  opacity: 0.3;
  background-color: #3dc8ff;
}
.button.secondary:disabled:not([data-progress=true]):hover, .button.secondary[aria-disabled=true]:not([data-progress=true]):hover {
  background-color: #22b5f0;
}
.button.tertiary {
  --bg-color: #d1d6e0;
  color: #000083;
}
.button.tertiary:hover, .button.tertiary:focus {
  background-color: #dce0eb;
}
.button.tertiary:disabled:not([data-progress=true]), .button.tertiary[aria-disabled=true]:not([data-progress=true]) {
  opacity: 0.5;
  background-color: #dce0eb;
}
.button.tertiary:disabled:not([data-progress=true]):hover, .button.tertiary[aria-disabled=true]:not([data-progress=true]):hover {
  background-color: #d1d6e0;
}
.button.tertiary-white {
  --bg-color: #fff;
  border: 1px solid #d1d6e0;
  color: #000083;
}
.button.tertiary-white:hover, .button.tertiary-white:focus {
  background-color: #f2f4fa;
  border-color: #f2f4fa;
}
.button.tertiary-white:disabled:not([data-progress=true]), .button.tertiary-white[aria-disabled=true]:not([data-progress=true]) {
  opacity: 0.5;
  background-color: #dce0eb;
}
.button.tertiary-white:disabled:not([data-progress=true]):hover, .button.tertiary-white[aria-disabled=true]:not([data-progress=true]):hover {
  background-color: #d1d6e0;
}
.button.large {
  font-size: 1.25rem;
  padding: 1.125rem 2rem;
}
@media (max-width: 840px) {
  .button.large.responsive {
    font-size: 1rem;
    padding: 0.7692307692rem 1rem;
  }
}
@media (max-width: 480px) {
  .button.large.responsive {
    font-size: 0.75rem;
    padding: 0.5rem 0.625rem;
  }
}
.button.medium {
  font-size: 1rem;
  padding: 0.75rem 1rem;
}
@media (max-width: 480px) {
  .button.medium.responsive {
    font-size: 0.75rem;
    padding: 0.5rem 0.5882352941rem;
  }
}
.button.small {
  font-size: 0.75rem;
  padding: 0.625rem 0.5rem;
}
.button.extra-small {
  font-size: 0.625rem;
  padding: 0.4375rem 0.25rem;
}
.button[class*=icon-] {
  padding: 0.75rem;
  line-height: 0.7;
  font-size: 2rem;
}
.button[class*=icon-][class*=medium] {
  padding: 0.5rem;
  font-size: 1.5rem;
}
@media (max-width: 360px) {
  .button[class*=icon-][class*=medium] {
    padding: 0.25rem;
    font-size: 1.25rem;
  }
}
.button[class*=icon-][class*=small] {
  font-size: 1.5rem;
  padding: 0.25rem;
}
.button[class*=icon-][class*=extra-small] {
  font-size: 1rem;
}
.button[class*=icon-][class*=round] {
  border-radius: 100%;
}
.button[class*=icon-][class*=naked] {
  border: none;
  background: transparent;
}
@media (max-width: 600px) {
  .button.modalStyles {
    font-size: 0.75rem;
    padding: 0.625rem 0.5rem;
  }
}
.button span {
  align-content: center;
  display: flex;
  justify-content: space-between;
}
.button span i {
  line-height: 0.7;
}
.button .iconLeft {
  margin-right: 0.5rem;
}
.button .loadingIcon {
  position: absolute;
  left: 50%;
  top: 50%;
  translate: -50% -50%;
  animation: rotating 1s linear infinite;
}
.button.fullWidth {
  width: 100%;
  text-align: center;
}
.button.fullWidth span {
  justify-content: center;
}

.cursorDefault {
  cursor: default;
}

.downloadLink.disabled {
  pointer-events: none;
  cursor: default;
}

.align_left, .align_bottom-left {
  text-align: left;
}
.align_center, .align_bottom-center {
  text-align: center;
}
.align_right, .align_bottom-right {
  text-align: right;
}

.progress {
  pointer-events: none;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}

.gradient {
  left: 0;
  background: linear-gradient(270deg, #94d232 0%, #22b5f0 100%);
  animation: flicker 8s ease-in-out infinite;
}

.bar {
  right: 0;
  background-color: var(--bg-color);
}

.percentage {
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-weight: 600;
  font-size: 1.25rem;
}

@keyframes flicker {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}