@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container {
  position: relative;
}
.container .helperText {
  display: block;
  text-align: right;
  margin-bottom: 0.25rem;
  font-size: 0.75rem;
}
.container .helperText b {
  color: #0a101a;
  font-weight: 600;
}

.currencyContainer {
  position: absolute;
  height: 52px;
  top: 0;
  right: 1rem;
  display: flex;
  align-items: center;
  font-family: "museo-sans", sans-serif;
  color: rgba(10, 16, 26, 0.6);
}