@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.wrapper {
  border: 1px solid #aeb6c2;
  border-radius: 0.125rem;
  position: relative;
  width: 100%;
  display: flex;
  background-color: #fff;
}
.wrapper.focus {
  outline: 1px solid #000083;
  border-color: #000083;
}
.wrapper.withError {
  border-color: #bd0b23;
}
.wrapper.disabled {
  border-style: dashed;
}
.wrapper .input {
  width: 100%;
  border: none;
  padding: 1rem 0 1rem 1rem;
  background-color: #fff;
  border-radius: 0.125rem;
}
.wrapper .input.withIcon {
  padding-left: 0.75rem;
}
.wrapper .input:focus {
  outline: none;
}
.wrapper .input.disabled {
  background-color: #e9edf6;
  color: rgba(10, 16, 26, 0.6);
}
.wrapper .label {
  position: absolute;
  color: rgba(10, 16, 26, 0.6);
  pointer-events: none;
  left: 1rem;
  top: 1rem;
  transition: 0.2s ease all;
  text-transform: none;
  font-size: 1rem;
  font-weight: 500;
  letter-spacing: unset;
  white-space: nowrap;
  max-width: calc(100% - 4rem);
  overflow: hidden;
  text-overflow: ellipsis;
}
.wrapper .label.animated {
  top: -0.5rem;
  left: 0.5rem;
  background-color: #fff;
  padding-inline: 0.5rem;
  font-size: 0.75rem;
  max-width: calc(100% - 2rem);
}
.wrapper .label.withIcon {
  left: calc(1rem + 30px);
}

.cursorPointer {
  cursor: pointer;
}

.input:-webkit-autofill,
.input:-webkit-autofill:focus {
  transition: background-color 0s 600000s, color 0s 600000s;
}

.options {
  z-index: 1;
  position: absolute;
  left: -1px;
  top: 48px;
  background-color: #fff;
  width: 100%;
  border: 1px solid #d1d6e0;
  margin-top: 0.5rem;
  border-radius: 0.25rem;
  max-height: 20rem;
  overflow-y: auto;
  margin-bottom: 1rem;
  box-shadow: 4px 4px 10px 0 rgba(0, 0, 0, 0.2);
}
.options .option {
  border-top: 1px solid #d1d6e0;
  display: flex;
  align-items: center;
  padding: 1rem;
}
.options .option.active {
  background-color: #e9edf6;
}
.options .option:first-child {
  border-top: none;
}
.options .option:hover {
  background-color: #e9edf6;
  cursor: pointer;
}
.options .option.disableBorderTop {
  border-top: none;
}
.options .optionFocus {
  background-color: #e9edf6;
  cursor: pointer;
}
.options .disabled {
  pointer-events: none;
  opacity: 0.5;
}

.icon {
  margin: auto 0 auto 1rem;
}

.icons {
  display: flex;
  align-items: center;
  padding-inline: 0.3rem;
}

.icons:hover {
  cursor: pointer;
}

.searchIcon {
  padding-right: 1rem;
}

.caretBody {
  height: 100%;
  width: 3rem;
}

.fade {
  background: linear-gradient(90deg, black 90%, white 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.activeInputColor {
  color: #00004f;
}