.field {
  padding: 1rem 0;
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
}
.field__checkbox {
  margin: -0.25rem 0.5rem 0 0;
}
.field.hidden {
  display: none;
}
.field--half {
  width: 50%;
}
@media (max-width: 600px) {
  .field--full-small {
    width: 100%;
  }
}
.field__input-wrapper {
  width: 100%;
  position: relative;
  flex: 1 1 auto;
  padding: 0 1px;
}
.field__input-wrapper--with-icon .input-icon {
  position: absolute;
  bottom: 13px;
  font-size: 1.25rem;
  left: 0.8rem;
  color: #717c8f;
}
.field__input-wrapper--with-icon input,
.field__input-wrapper--with-icon select {
  padding-left: 2.6rem;
}
.field__input-wrapper--with-icon input:focus ~ .input-icon,
.field__input-wrapper--with-icon select:focus ~ .input-icon {
  color: #000083;
}
.field__input-wrapper--with-icon--right .input-icon {
  right: 0.8rem;
  left: unset;
}
.field__input-wrapper--with-icon--right input,
.field__input-wrapper--with-icon--right select {
  padding-right: 2.6rem;
  padding-left: 1rem;
}
.field__input-wrapper--with-adornment input,
.field__input-wrapper--with-adornment select {
  padding-right: 2.6rem;
}
.field__input-wrapper--dark .field__label {
  color: #fff;
}
.field__input-wrapper--dark .field__input:focus {
  border: 1px solid #22b5f0;
  box-shadow: 0 0 0 1px #22b5f0;
}
.field__input-wrapper .has-tooltip {
  position: absolute;
  top: -5px;
  right: 0;
}
.field__input-wrapper .has-tooltip .tooltip {
  visibility: hidden;
  opacity: 0;
}
.field__input-wrapper .has-tooltip:hover .tooltip {
  opacity: unset;
  visibility: visible;
}
.field__input-wrapper .has-tooltip::before {
  font-family: icons !important;
  font-style: normal;
  font-weight: normal !important;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000083;
}
.field button {
  font-size: 0.75rem;
  vertical-align: bottom;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  padding: 1.25rem;
  margin-left: 1px;
  margin-bottom: -1px;
}
.field button:hover, .field button:focus {
  border-color: #3dc8ff;
  box-shadow: none;
}
.field button:disabled {
  margin-left: 0;
}
.field textarea {
  resize: vertical;
}
.field__label {
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  font-size: 0.625rem;
  font-weight: 900;
  font-family: "museo-sans", sans-serif;
  margin-bottom: 0.25rem;
  letter-spacing: 1px;
}
.field__label__helper {
  font-family: "interface", sans-serif;
  font-weight: 300;
  color: rgba(10, 16, 26, 0.6);
}
.field__label--animated {
  z-index: 1;
  color: rgba(10, 16, 26, 0.6);
  position: absolute;
  pointer-events: none;
  left: 1rem;
  top: 1rem;
  transition: 0.2s ease all;
  text-transform: none;
  font-size: 1rem;
  font-weight: 500;
  letter-spacing: unset;
}
.field__label--animated--focused {
  top: -0.5rem;
  left: 0.5rem;
  background-color: #fff;
  padding-inline: 0.5rem;
  font-size: 0.75rem;
}
.field__input {
  width: 100%;
  box-sizing: border-box;
  -webkit-appearance: none;
  border: 1px solid transparent;
  border-radius: 0.125rem;
  padding: 13px 1rem 11px;
  color: #0a101a;
  font-family: "museo-sans", sans-serif;
  font-size: 1rem;
  box-shadow: 0 0 0 1px #aeb6c2;
  line-height: 1.25;
}
.field__input::placeholder {
  color: rgba(10, 16, 26, 0.6);
}
