@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.footerRow {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 0.625rem;
}
.footerRow p {
  font-family: "interface", sans-serif;
  font-size: 0.75rem;
  font-weight: 400;
  color: #0a101a;
  margin: 0;
}
.footerRow i {
  font-size: 1.2rem;
  margin-right: 0.55rem;
}
.footerRow button {
  margin-left: auto;
}

.validationMessage {
  display: flex;
  align-items: center;
}