@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.elementCard {
  background-color: #fff;
  box-sizing: border-box;
  overflow: hidden;
}
.elementCard img {
  display: block;
}
.elementCardIsFullHeight {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.contentContainer {
  padding: 1rem;
}
.contentContainerIsFullHeight {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.linkedIconAndText {
  margin-bottom: 0.25rem;
}

.visibleOverflow {
  overflow: visible;
}

.isHorizontallyAligned {
  display: flex;
}

.imageContainer {
  position: relative;
}

.horizontalImageContainer {
  display: flex;
  align-items: center;
  min-width: 64px;
  max-width: 560px;
}
.horizontalImageContainer * {
  width: 100%;
}

.imageAlignTop {
  align-items: flex-start;
}

.imageAlignBottom {
  align-items: flex-end;
}

.buttonBottomAlignment {
  display: flex;
  flex-direction: column-reverse;
  flex-grow: 1;
}

.dropShadow {
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}

.stroke {
  border: 1px solid #d1d6e0;
  transition: 0.5s;
}

.zoomImageOnHover {
  color: unset;
}
.zoomImageOnHover .image {
  overflow: hidden;
}
.zoomImageOnHover .image picture > img {
  transition: 0.5s;
}
@media (hover: hover) {
  .zoomImageOnHover:hover .image picture > img {
    transform: scale(1.1);
  }
}

.dropShadowOnHover {
  border: none;
  composes: dropShadow;
}
@media (hover: hover) {
  .dropShadowOnHover:hover.dropShadow {
    box-shadow: 8px 8px 10px 0px rgba(0, 0, 0, 0.3);
  }
}

.strokeOnHover {
  box-shadow: none;
  composes: stroke;
}
@media (hover: hover) {
  .strokeOnHover:hover.stroke {
    border: 1px solid #000083;
  }
}