.totalBar {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 60px;
  background-color: #e9edf6;
  border-top: 1px solid #d1d6e0;
  border-bottom: 1px solid #d1d6e0;
  font-family: "museo-sans", sans-serif;
  display: flex;
  align-items: center;
  padding: 1rem;
  box-sizing: border-box;
  cursor: pointer;
  user-select: none;
  box-shadow: 0 -4px 8px 0 rgba(0, 0, 0, 0.1);
  transition: box-shadow 300ms ease-out;
}
.totalBar.integrated {
  position: relative;
  background-color: transparent;
  border: none;
  padding: 0;
  height: auto;
  margin: 22px 0 6px;
  cursor: auto;
  box-shadow: none;
}
.totalBar.integrated.ebook {
  margin-top: 10px;
}
.totalBar.expanded {
  box-shadow: none;
}

.wrapper {
  display: flex;
  align-items: baseline;
  flex: 1;
  gap: 0.5rem;
  transition: opacity 300ms;
}
.wrapper.loading {
  opacity: 0.3;
}

.total {
  font-size: 1.25rem;
  font-weight: 700;
  flex-shrink: 2;
}
@media (min-width: 840px) {
  .total {
    font-size: 1.5rem;
  }
}

.per {
  font-size: 0.875rem;
  font-weight: 400;
  flex: 1;
}
@media (min-width: 840px) {
  .per {
    font-size: 1rem;
  }
}

.ebookHeadline {
  margin: 0;
  flex: 1;
