@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.requirements {
  margin: 1.5rem 0 0.5rem 0;
  line-height: 1.5;
}

.requirementsValue {
  color: rgba(10, 16, 26, 0.6);
  text-transform: initial;
  font-size: 1rem;
}
.requirementsValue * {
  margin: 0;
}
@media (max-width: 600px) {
  .requirementsValue {
    font-size: 0.875rem;
  }
}

.requirementsLabel {
  font-size: 0.75rem;
  text-transform: uppercase;
  font-weight: 600;
}

.dropzone {
  border-radius: 0.125rem;
  border: 2px #22b5f0 dashed;
  background-color: rgba(34, 181, 240, 0.1);
  outline: none;
  transition: border 0.24s ease-in-out;
  text-align: center;
}

.dropzoneDescription {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  line-height: 1.1;
  color: rgba(10, 16, 26, 0.6);
  padding: 1.25rem;
  margin: 0;
  font-family: "museo-sans", sans-serif;
  font-size: 1.25rem;
  box-sizing: border-box;
}
.dropzoneDescription > * {
  margin-right: 1rem;
}
.dropzoneDescription > *:last-child {
  margin-right: 0;
}
.dropzoneDescription i {
  font-size: 3rem;
  color: #000083;
  margin-right: 1rem;
}
.dropzoneDescription :global button.link {
  text-decoration: underline;
  cursor: pointer;
  font-size: 1.25rem;
}
@media (max-width: 600px) {
  .dropzoneDescription {
    font-size: 1rem;
  }
  .dropzoneDescription :global button.link {
    font-size: 1rem;
  }
  .dropzoneDescription i {
    font-size: 2rem;
  }
}
@media (max-width: 480px) {
  .dropzoneDescription :global button.link {
    display: block;
  }
}

.dropzonePercentage {
  color: rgba(10, 16, 26, 0.6);
  font-weight: 500;
  font-size: 1rem;
}

.uploadLabel {
  margin-top: 0;
}

.fileInfo {
  font-weight: 600;
  word-break: break-all;
  margin-bottom: 1.5rem;
}
@media (max-width: 600px) {
  .fileInfo {
    font-size: 0.875rem;
  }
}

.filePageCount {
  margin-top: 0.75rem;
}

.loading .dropzoneDescription {
  color: #000083;
  justify-content: space-between;
  padding: 0.5rem 0;
}
.loading .dropzoneDescription span {
  display: flex;
  align-items: center;
  justify-content: center;
}

.loadingBar {
  height: 0.5rem;
  border-radius: 0.125rem;
  background-color: #22b5f0;
  position: relative;
  overflow: hidden;
}

.loadingContent {
  display: block;
  position: absolute;
  width: 200px;
  height: 0.5rem;
  border-radius: 0.125rem;
  background-color: #000083;
  animation: loader 3s ease infinite;
}

.loadingContentProgress {
  composes: loadingContent;
  width: auto;
  animation: none;
  transition: 0.3s;
}

.fileName {
  text-align: right;
  color: rgba(10, 16, 26, 0.6);
  line-height: 1.5;
  margin: 0.5rem 0;
}

.cancel {
  display: block;
  margin: 0 auto;
}

.disabled {
  opacity: 0.4;
  pointer-events: none;
}

.message {
  font-family: "interface", sans-serif;
}

@media (max-width: 1024px) {
  .uploadLabel {
    font-size: 1rem;
  }
}
@media (max-width: 600px) {
  .uploadLabel {
    margin-bottom: 0;
  }
}
@keyframes loader {
  from {
    left: -200px;
  }
  30% {
    width: 300px;
  }
  70% {
    width: 200px;
  }
  to {
    left: 100%;
  }
}