@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.listPriceForm,
.listPrice {
  display: flex;
  justify-content: space-between;
  padding: 0 1rem;
  min-width: 600px;
}

.fieldContainer,
.column {
  width: 15%;
  padding-bottom: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.fieldContainer .minPriceLabel,
.column .minPriceLabel {
  color: #0a101a;
  text-align: center;
  font-size: 0.75rem;
  font-weight: 300;
}
.fieldContainer :global .field__input,
.column :global .field__input {
  text-align: right;
  padding-right: 0.5rem;
  padding-left: 0.25rem;
}
.fieldContainer .listPriceLabel,
.column .listPriceLabel {
  margin-bottom: 1rem;
  text-align: center;
  font-weight: bold;
  font-family: "museo-sans", sans-serif;
}