@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.wrapper {
  display: grid;
  grid-template-columns: 4rem 1fr;
  border: 1px solid;
  border-radius: 0.125rem;
}
.wrapper.withChildren {
  border-radius: 0.125rem 0.125rem 0 0;
}
.wrapper.noRightBorderRadius {
  border-radius: 0.125rem 0 0 0 !important;
}
.wrapper.noBorderRadiusOnNotification {
  border-radius: 0 !important;
}
.wrapper.stacked {
  border-radius: 0;
}
.wrapper.lastNotificationWithoutChildren {
  border-radius: 0 0 0.125rem 0.125rem;
}
.wrapper.lastNotificationWithChildren {
  border-radius: 0 0 0.125rem 0;
}
.wrapper.oneNotificationWithChildren {
  border-radius: 0.125rem;
  border-bottom-left-radius: 0;
}
.wrapper .iconWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
}
.wrapper .iconWrapper i {
  font-size: 1.4rem;
}
.wrapper .message {
  padding: 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  color: rgba(10, 16, 26, 0.8);
  font-family: "interface", sans-serif;
}
.wrapper.neutral {
  border-color: #4a009e;
  background-color: #f7f3fb;
}
.wrapper.neutral .iconWrapper {
  background-color: #4a009e;
}
.wrapper.success {
  border-color: #008f62;
  background-color: #f2faf8;
}
.wrapper.success .iconWrapper {
  background-color: #008f62;
}
.wrapper.warning {
  border-color: #f05400;
  background-color: #fff8f2;
}
.wrapper.warning .iconWrapper {
  background-color: #f05400;
}
.wrapper.error {
  border-color: #bd0b23;
  background-color: #fdf3f5;
}
.wrapper.error .iconWrapper {
  background-color: #bd0b23;
}

.border {
  border-left: 3px solid;
  border-bottom-left-radius: 0.125rem;
}
.border > *:first-child {
  border-left: none;
  border-top: none;
  border-radius: 0;
}
.border-neutral {
  border-color: #4a009e;
}
.border-success {
  border-color: #008f62;
}
.border-warning {
  border-color: #f05400;
}
.border-error {
  border-color: #bd0b23;
}