@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container {
  display: flex;
  justify-content: space-between;
  flex: 1;
  gap: 0.5rem;
}
.container.active {
  background-color: #e9edf6;
}

.header {
  font-family: "museo-sans", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  margin: 0 0 0.5rem;
}
.header.activeHeader {
  color: #000083;
}

.description {
  font-family: "museo-sans", sans-serif;
  font-weight: 300;
  font-size: 0.875rem;
  margin: 0;
  line-height: 1.5;
}

.startingAtColumn {
  min-width: 85px;
}

.startingAt {
  text-align: right;
  font-family: "museo-sans", sans-serif;
  font-weight: 300;
  text-transform: uppercase;
  font-size: 0.75rem;
  margin: 0 0 0.5rem;
}

.money {
  font-family: "museo-sans", sans-serif;
  font-weight: 600;
  text-align: right;
  font-size: 1rem;
  margin: 0;
}