@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.referenceIconContainer {
  margin-bottom: 1.375rem;
}

.wrapper {
  border: 1px solid #d1d6e0;
  border-radius: 2px;
  background: #fff;
}
@media (min-width: 1024px) {
  .wrapper {
    display: grid;
    grid-template-columns: 2fr 1fr;
  }
}
.wrapper .previewContainer {
  background: #d1d6e0;
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 260px;
}
@media (min-width: 480px) {
  .wrapper .previewContainer {
    min-height: 410px;
  }
}
@media (min-width: 840px) {
  .wrapper .previewContainer {
    min-height: 510px;
  }
}
.wrapper .previewContainer img,
.wrapper .previewContainer picture {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.wrapper .previewContainer > picture:first-child {
  position: relative;
}
.wrapper .previewContainer > picture:first-child img {
  position: relative;
}
.wrapper .previewContainer .comparisonImage {
  opacity: 0.6;
}
.wrapper .previewContainer .toggle {
  position: absolute;
  bottom: 0.5rem;
  left: 0.53125rem;
  color: #0a101a;
  align-self: flex-end;
}
.wrapper .previewContainer .toggle > div:first-child {
  background-color: #0a101a;
  color: #fff;
  font-size: 0.75rem;
  gap: 0.25rem;
}
.wrapper .previewContainer .toggle > span {
  font-size: 0.75rem;
}
.wrapper .detailsContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.wrapper .detailsContainer .infoContainer {
  padding: 1rem;
}
.wrapper .detailsContainer .infoContainer .header {
  border-bottom: 1px solid #d1d6e0;
  position: relative;
  width: 100%;
}
.wrapper .detailsContainer .infoContainer .header h3 {
  margin: 0;
  margin-bottom: 0.5rem;
}
.wrapper .detailsContainer .infoContainer .header i {
  position: absolute;
  top: 0;
  right: 0;
}
.wrapper .detailsContainer .infoContainer .description {
  padding: 1rem 0;
}
.wrapper .detailsContainer .infoContainer .dropdownContainer {
  margin-top: 1rem;
}
.wrapper .detailsContainer .infoContainer .dropdownContainer > div:first-child {
  font-size: 0.75rem;
}
.wrapper .detailsContainer .comparisonContainer {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.wrapper .detailsContainer .comparisonContainer .header {
  background: #18263d;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #d1d6e0;
  cursor: pointer;
}
.wrapper .detailsContainer .comparisonContainer .header h4 {
  margin: 1rem;
  color: #fff;
}
.wrapper .detailsContainer .comparisonContainer .header i {
  padding: 0.625rem 0.5rem;
  border-left: 1px solid #d1d6e0;
}
.wrapper .detailsContainer .comparisonContainer .details {
  background: #37455c;
  padding: 1rem;
  border-radius: 0 0 2px 0;
  border-top: 1px dashed #d1d6e0;
}
.wrapper .detailsContainer .comparisonContainer .details .comparisonHeader {
  margin-bottom: 1rem;
  position: relative;
}
.wrapper .detailsContainer .comparisonContainer .details .comparisonHeader h3 {
  margin: 0;
  color: #fff;
  padding-bottom: 0.5rem;
}
.wrapper .detailsContainer .comparisonContainer .details .comparisonHeader i {
  position: absolute;
  top: 0;
  right: 0;
}
.wrapper .detailsContainer .comparisonContainer .details .dropdownContainer {
  margin-top: 1rem;
}
.wrapper .detailsContainer .comparisonContainer .details .dropdownContainer label {
  color: #fff;
}