@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.iconWithMetadata {
  font-size: 1.5rem;
  margin-left: 0.5rem;
}

.iconDescription {
  font-size: 0.75rem;
  color: rgba(10, 16, 26, 0.6);
  margin-left: 0.5rem;
}

.iconContainer {
  display: flex;
  align-items: center;
  margin-bottom: -0.25rem;
}
.iconContainer a:hover {
  text-decoration: none;
}
@media (max-width: 600px) {
  .iconContainer {
    position: relative;
    margin-bottom: -0.25rem;
  }
}