@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.starRating {
  display: flex;
  align-items: center;
}

.starButton {
  display: inline-flex;
}

.stars i {
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: #aeb6c2;
  font-size: 1.5rem;
  color: transparent;
}
.stars i.checked {
  color: #ffd908;
}

.stars:hover i {
  color: #ffd908;
}

.stars i:hover ~ i {
  color: transparent;
}

.starsDisabled {
  composes: stars;
  pointer-events: none;
}