@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.wrapper {
  display: inline-flex;
  cursor: pointer;
}

.radioInputWrapper {
  background-color: #fff;
  border: 2px solid #000083;
  border-radius: 50%;
  box-sizing: border-box;
  height: var(--size);
  width: var(--size);
  min-width: var(--size);
  min-height: var(--size);
  position: relative;
}
.radioInputWrapper input {
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
  opacity: 0;
  width: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.radioInputWrapper input:checked {
  box-sizing: content-box;
  opacity: 1;
  height: 66.6%;
  width: 66.6%;
  border-radius: 50%;
  background-color: #000083;
}

.disabled {
  cursor: default;
}
.disabled .radioInputWrapper {
  border-color: #aeb6c2;
}
.disabled .radioInputWrapper input:checked {
  background-color: #aeb6c2;
}