@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.toggle {
  display: flex;
  align-items: center;
}

.input {
  min-width: 56px;
  height: 1.5rem;
  border-radius: 1rem;
  font-size: 0.625rem;
  font-weight: bold;
  text-transform: uppercase;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  padding: 0.25rem;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
}

.toggleInactive {
  composes: toggle;
  color: rgba(10, 16, 26, 0.6);
}
.toggleInactive .input {
  color: rgba(10, 16, 26, 0.7);
  background-color: #c0c6d1;
}

.inputInactive {
  composes: input;
  flex-direction: row;
}

.inputText {
  width: calc(100% - 1.5rem);
  text-align: center;
}

.inputCircle {
  display: block;
  width: 1.15rem;
  height: 1.15rem;
  background-color: #fff;
  border-radius: 1.5rem;
}

.label {
  text-transform: capitalize;
}
@media (max-width: 480px) {
  .label {
    font-size: 0.875rem;
  }
}

.inputType-bleed,
.inputType-wrap {
  background-color: #22b5f0;
  color: #000083;
}

.inputType-folds,
.inputType-holePunch {
  background-color: #a41cad;
}

.inputType-margin {
  background-color: #000083;
}

.inputType-trim {
  background-color: #0a101a;
}

.inputType-all {
  background-color: #525f75;
}

.labelAlign-left {
  flex-direction: row-reverse;
  justify-content: space-between;
}
.labelAlign-left > :last-child {
  margin-right: 0.5rem;
}

.labelAlign-right {
  justify-content: flex-start;
}
.labelAlign-right > :last-child {
  margin-left: 0.5rem;
}