@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.content, .contentCollapsed {
  position: relative;
}
.content *, .contentCollapsed * {
  font-family: "museo-sans", sans-serif;
}
.content p:first-of-type, .contentCollapsed p:first-of-type {
  margin-top: 0;
}
.contentCollapsed {
  overflow: hidden;
}
.contentCollapsed::after {
  content: "";
  position: absolute;
  top: 25%;
  left: 0;
  width: 100%;
  height: 75%;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0), var(--color, #fff));
}

.actions {
  margin-top: 0.5rem;
  display: flex;
  justify-content: flex-end;
}

.multipleActions {
  justify-content: space-between;
}

.collapseToggle {
  display: flex;
}

.collapseLink {
  font-family: "museo-sans", sans-serif;
}
@media (max-width: 480px) {
  .collapseLink {
    font-size: 0.875rem;
  }
}