@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container {
  background-color: #fff;
  width: 264px;
  padding: 1.5rem;
}

.label {
  color: #0a101a;
  font-size: 1rem;
  font-stretch: normal;
  font-style: normal;
  font-weight: bold;
  letter-spacing: normal;
  line-height: 1;
  margin-bottom: 1rem;
}

.colorButtonsContainer {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.button {
  background-color: #dce0eb;
  border: 2px solid #dce0eb;
  border-radius: 2px;
  color: #000083;
  font-size: 12px;
  font-stretch: normal;
  font-style: normal;
  font-weight: bold;
  letter-spacing: normal;
  line-height: 1.33;
  padding: 0.5rem;
  width: 100%;
  cursor: pointer;
}
.button:hover {
  border: 2px solid #e9edf6;
  background-color: #e9edf6;
}
.button:focus {
  box-shadow: none;
}

.buttonActive {
  composes: button;
  background-color: #fff;
  border: 2px solid #000083;
}

.buttonArrow {
  display: none;
  height: 8px;
  margin: 0 auto 5px;
  overflow: hidden;
  position: relative;
  bottom: 2px;
  width: 15px;
}
.buttonArrow::before {
  background: white;
  border: 2px solid #000083;
  content: "";
  height: 10px;
  position: absolute;
  bottom: 3px;
  transform: rotate(45deg);
  width: 10px;
  z-index: 2;
}

.buttonArrowActive {
  composes: buttonArrow;
  display: block;
}

.buttonContainer {
  width: 120px;
}
.buttonContainer:hover .buttonArrowActive {
  visibility: hidden;
}

.colorsContainer {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

.colorsContainerRow {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.25rem;
}
.colorsContainerRow:last-child {
  margin-bottom: 0;
}

.spacer {
  height: 1px;
  background-color: #d1d6e0;
  margin: 1.5rem 0;
}

.moreOptionsContainer {
  align-items: center;
  box-sizing: border-box;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.moreOptionsButton {
  align-items: center;
  border-radius: 2px;
  display: flex;
  height: 30px;
  justify-content: center;
  width: 30px;
  background-color: #000083;
  border: 2px solid transparent;
}
.moreOptionsButton i {
  color: #fff;
  font-size: 1.5rem;
}

.moreOptionsButtonActive {
  composes: moreOptionsButton;
  background-color: #fff;
  border-radius: 2px;
  border: 2px solid #000083;
}
.moreOptionsButtonActive i {
  color: #000083;
}

.moreOptionsButton:hover {
  background-color: #2424b5;
}
.moreOptionsButton:hover i {
  color: #fff;
}

.moreOptionsLabel {
  font-weight: 600;
  line-height: 1;
}

.helpersContainer {
  display: flex;
  justify-content: space-between;
}

.helperHex {
  display: flex;
}

.helperRgb {
  display: flex;
  justify-content: flex-end;
  width: 100%;
}

.helperInput {
  color: rgba(10, 16, 26, 0.6);
  display: flex;
  flex-direction: column;
  font-stretch: normal;
  font-style: normal;
  font-weight: 500;
  letter-spacing: normal;
}
.helperInput label {
  font-weight: 300;
  letter-spacing: 1px;
  color: inherit;
  font-size: 0.625rem;
  line-height: 1;
  margin-top: 0.5rem;
  text-align: center;
  text-transform: uppercase;
}

.helperInputContainer {
  align-items: center;
  border: 1px solid #d1d6e0;
  border-radius: 2px;
  display: flex;
  padding: 6px 8px;
  -webkit-user-select: all;
  -moz-user-select: all;
  -ms-user-select: all;
  user-select: all;
}
.helperInputContainer input {
  background-color: #fff;
  border: none;
  color: inherit;
  line-height: 1.25;
  padding: 0;
  text-align: right;
  text-transform: uppercase;
  width: 100%;
}
.helperInputContainer input:focus {
  outline: none;
}

.colorPickerWrapper {
  border-radius: 2px;
  height: 160px;
  margin-bottom: 1.5rem;
  position: relative;
}
.colorPickerWrapper > div {
  height: 160px;
  width: 100%;
  flex-direction: column-reverse;
}
.colorPickerWrapper > div > div {
  border-radius: 0;
}
.colorPickerWrapper > div > div > div > div {
  border-width: 3px;
  height: 16px;
  width: 16px;
}
.colorPickerWrapper > div > div:first-of-type {
  margin-top: 1rem;
  border-bottom: 0;
}

.colorBubbleWrapper {
  align-items: center;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  height: 32px;
  width: 32px;
  border: 2px solid transparent;
}
.colorBubbleWrapper:hover {
  border: 2px solid #000083;
}
.colorBubbleWrapper:last-child {
  margin-right: 0;
}

.colorBubbleWrapperActive {
  composes: colorBubbleWrapper;
  border: 2px solid #000083;
}

.colorBubble {
  border: solid 1px #525f75;
  border-radius: 50%;
  height: 24px;
  width: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.colorBubble.transparent::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  height: 2px;
  width: 100%;
  background-color: #d6223a;
}

.recentlyUsedColors {
  margin-bottom: 1rem;
}