@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.carousel {
  position: relative;
  overflow: hidden;
  box-sizing: border-box;
}
@media (min-width: 1024px) {
  .carousel {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}
.carousel .inner {
  white-space: nowrap;
  transform: translateX(calc(-1 * var(--activeIndex, 0) * 100%));
  position: relative;
  transition: transform 0.7s;
  width: 100%;
}
@media (min-width: 1024px) {
  .carousel .inner {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}
.carousel .inner div {
  vertical-align: top;
  position: relative;
  min-height: 100%;
}
.carousel .inner div .carouselItem {
  display: inline-flex;
  flex-direction: row;
  width: 100%;
}
.carousel .inner div .carouselItem picture,
.carousel .inner div .carouselItem img {
  min-width: 100%;
  min-height: 100%;
}
@media (min-width: 1024px) {
  .carousel .inner div .carouselItem {
    background-position: 50% 50%;
    background-size: cover;
    background-repeat: no-repeat;
  }
}

.container {
  display: flex;
  flex-direction: column;
  border: 1px solid #d1d6e0;
  box-sizing: border-box;
}
@media (min-width: 1024px) {
  .container {
    display: grid;
    grid-template-columns: 1fr 460px;
    grid-template-areas: "images description";
  }
}
.container > * {
  box-sizing: border-box;
}
.container .imagesContainer {
  grid-area: images;
  position: relative;
  overflow: hidden;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  height: 100%;
}
.container .infoContainer {
  grid-area: description;
  padding: 2rem;
  border-top: 1px solid #d1d6e0;
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  font-family: "museo-sans", sans-serif;
}
@media (min-width: 1024px) {
  .container .infoContainer {
    flex-direction: column;
    width: 100%;
    max-width: 460px;
    border-top: none;
    border-left: 1px solid #d1d6e0;
  }
}
@media (max-width: 600px) {
  .container .infoContainer {
    padding: 1.5rem;
    padding-top: 1rem;
  }
}
.container .infoContainer .headerContainer {
  display: flex;
  gap: 1rem;
  justify-content: space-between;
  border-bottom: 1px solid #d1d6e0;
  padding-bottom: 1rem;
}
@media (max-width: 1024px) {
  .container .infoContainer .options {
    overflow: hidden;
    margin-inline: -1.5rem;
  }
}
.container .infoContainer .options .optionsTitle {
  font-weight: 900;
  text-transform: uppercase;
  color: rgba(10, 16, 26, 0.8);
  font-size: 0.625rem;
  margin: 1rem 0;
  letter-spacing: 1px;
}
.container .infoContainer .options .previewColorsContainer {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}
@media (max-width: 1024px) {
  .container .infoContainer .options .previewColorsContainer {
    margin-inline: 1.5rem;
    margin-bottom: 2rem;
    flex-wrap: wrap;
  }
}
@media (max-width: 600px) {
  .container .infoContainer .options .previewColorsContainer {
    margin-top: 0.5rem;
  }
}
.container .infoContainer .options .previewColorsContainer .colorSelector {
  border: 2px solid rgba(10, 16, 26, 0.1);
  border-radius: 50%;
  padding: 0.125rem;
  cursor: pointer;
}
.container .infoContainer .options .previewColorsContainer .colorSelector div {
  border-radius: 50%;
  height: 15px;
  width: 15px;
}
@media (min-width: 480px) {
  .container .infoContainer .options .previewColorsContainer .colorSelector div {
    height: 18px;
    width: 18px;
  }
}
@media (hover: hover) {
  .container .infoContainer .options .previewColorsContainer .colorSelector:hover {
    border-color: #000083;
  }
}
.container .infoContainer .options .previewColorsContainer .selectedColor {
  border: 2px solid #000083;
}
.container .infoContainer .options .previewMobileImagesContainer {
  position: relative;
  margin-bottom: 2rem;
}
.container .infoContainer .options .previewMobileImagesContainer .sliderElements {
  height: 70px;
  display: flex;
  gap: 0.5rem;
}
@media (max-width: 600px) {
  .container .infoContainer .options .previewMobileImagesContainer .sliderElements {
    height: 40px;
    gap: 1rem;
  }
}
.container .infoContainer .options .previewMobileImagesContainer .sliderItem {
  border: 2px solid rgba(10, 16, 26, 0.1);
  padding: 0.125rem;
  border-radius: 1px;
  cursor: pointer;
}
.container .infoContainer .options .previewMobileImagesContainer .sliderItem img {
  width: auto;
  height: 100%;
}
@media (hover: hover) {
  .container .infoContainer .options .previewMobileImagesContainer .sliderItem:hover {
    border-color: #000083;
  }
}
.container .infoContainer .options .previewMobileImagesContainer .selectedItem {
  border: 2px solid #000083;
}
.container .infoContainer .options .previewMobileImagesContainer .buttons {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.container .infoContainer .options .previewMobileImagesContainer .buttons > button {
  position: absolute;
  z-index: 1000;
  height: 100%;
  width: 50px;
  top: 0;
  display: flex;
  align-items: center;
}
.container .infoContainer .options .previewMobileImagesContainer .buttons > button:focus {
  box-shadow: none;
}
.container .infoContainer .options .previewMobileImagesContainer .buttons .buttonLeft {
  background: linear-gradient(-270deg, #fff 0%, rgba(255, 255, 255, 0) 80%);
  left: 0;
}
.container .infoContainer .options .previewMobileImagesContainer .buttons .buttonRight {
  background: linear-gradient(270deg, #fff 0%, rgba(255, 255, 255, 0) 80%);
  right: 0;
  flex-direction: row-reverse;
  text-align: right;
}
.container .infoContainer .options .previewMobileImagesContainer .buttons .buttonRight span {
  text-align: right;
}
.container .infoContainer .options .previewImagesContainer {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 0.5rem;
  padding: 0.125rem;
  margin-bottom: 2rem;
}
@media (min-width: 840px) {
  .container .infoContainer .options .previewImagesContainer {
    margin-bottom: 0;
    grid-template-columns: repeat(3, 1fr);
  }
}
.container .infoContainer .options .previewImagesContainer picture,
.container .infoContainer .options .previewImagesContainer img {
  min-width: 100%;
  min-height: 100%;
}
.container .infoContainer .options .previewImagesContainer .item {
  border: 2px solid rgba(10, 16, 26, 0.1);
  border-radius: 1px;
  padding: 0.1875rem;
  height: calc(100% - 4px);
  min-width: 100px;
  cursor: pointer;
}
@media (hover: hover) {
  .container .infoContainer .options .previewImagesContainer .item:hover {
    border-color: #000083;
  }
}
.container .infoContainer .options .previewImagesContainer .selectedItem {
  border-color: #000083;
}

.reverseRow {
  flex-direction: column;
}
@media (min-width: 840px) {
  .reverseRow {
    grid-template-areas: "description images";
    grid-template-columns: 460px 1fr;
  }
}
@media (min-width: 840px) {
  .reverseRow .infoContainer {
    border-left: none;
    border-right: 1px solid #d1d6e0;
  }
}

.referenceIconContainer {
  margin-bottom: 1.375rem;
}