@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.bulkActionsMenu {
  display: flex;
  align-items: center;
}
@media (max-width: 360px) {
  .bulkActionsMenu {
    display: block;
    margin-right: 0;
  }
}

.label {
  display: flex;
  align-items: center;
  height: 2rem;
  margin-right: 1rem;
  margin-bottom: 1rem;
}
@media (max-width: 360px) {
  .label {
    margin-right: 0;
  }
}
.label label {
  color: rgba(10, 16, 26, 0.6);
  font-weight: bold;
}

.labelSelected {
  composes: label;
}
.labelSelected label {
  color: #0a101a;
}

.actions {
  display: flex;
  flex: 1;
  margin-bottom: 1rem;
}
.actions > * {
  margin-right: 1rem;
}
.actions > *:last-child {
  margin-right: 0;
}