@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.specsPanel {
  position: absolute;
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  width: 100%;
  background-color: #f2f4fa;
  border: 1px solid #d1d6e0;
  border-bottom: none;
  padding: 1rem;
  box-shadow: 0px -4px 8px 0px rgba(10, 16, 26, 0.1);
  bottom: 60px;
  transition: bottom 300ms ease-out;
}
@media (min-width: 840px) and (max-width: 1023px) {
  .specsPanel {
    flex-direction: row;
    gap: 40px;
    padding: 2rem;
  }
}
@media (min-width: 1024px) {
  .specsPanel {
    box-shadow: 0 4px 8px 0 rgba(10, 16, 26, 0.1);
    bottom: 0;
    border-bottom: 1px solid #d1d6e0;
    position: relative;
  }
}
.specsPanel h6 {
  text-transform: uppercase;
  color: rgba(10, 16, 26, 0.6);
  margin: 0;
  line-height: 1.33;
  letter-spacing: 0.5px;
}

.previewImage {
  display: block;
  width: 240px;
  height: 240px;
  overflow: hidden;
  margin: 0 auto;
  position: relative;
}
@media (max-height: 550px) {
  .previewImage {
    width: 120px;
    height: 120px;
  }
}
@media (min-width: 1024px) {
  .previewImage {
    width: 288px;
    height: 288px;
  }
}
@media (min-width: 840px) and (max-width: 1023px) {
  .previewImage {
    width: 290px;
    height: 290px;
    margin-bottom: 1rem;
  }
}
.previewImage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.unfinishedHint {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: rgba(211, 211, 211, 0.8);
  text-align: center;
  font-family: "museo-sans", sans-serif;
  font-size: 20px;
  color: rgba(10, 16, 26, 0.8);
}
.unfinishedHint > span {
  width: 55%;
}

@media (min-width: 840px) and (max-width: 1023px) {
  .specsAndActions {
    flex: 1;
  }
}

.specs {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 1.5rem;
}
@media (min-width: 840px) and (max-width: 1023px) {
  .specs {
    margin-top: 0;
  }
}

.title {
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  align-items: center;
}
.title > span {
  font-weight: 600;
  font-family: "museo-sans", sans-serif;
}

.specsTable {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto auto;
  font-size: 0.75rem;
  font-family: "museo-sans", sans-serif;
  gap: 10px;
}
.specsTable dt {
  text-transform: uppercase;
  color: rgba(10, 16, 26, 0.6);
  margin-bottom: 0.25rem;
  letter-spacing: 0.5px;
  line-height: 1.33;
}
.specsTable dd {
  margin: 0;
  font-size: 0.875rem;
  font-weight: 600;
  line-height: 1.25;
}
@media (min-width: 1024px) {
  .specsTable dd {
    font-size: 1rem;
  }
}
@media (min-width: 840px) and (max-width: 1023px) {
  .specsTable dd {
    font-size: 1rem;
    margin-top: 0.375rem;
  }
  .specsTable dd:empty {
    margin-top: 0;
  }
}
.specsTable:has(.ebookFileFormat) {
  gap: 0;
  margin: 0.5rem 0 0.5rem;
}
@media (max-height: 550px) {
  .specsTable dt:has(+ dd:not(:empty)) {
    display: none;
  }
}
@media (min-width: 840px) and (max-width: 1023px) {
  .specsTable {
    gap: 18px;
  }
}

.specRow {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.actions {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
  gap: 0.5rem;
}
@media (min-width: 840px) and (max-width: 1023px) {
  .actions {
    flex-direction: row;
    align-items: center;
  }
}

.subActions {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
@media (min-width: 600px) {
  .subActions {
    flex-direction: row;
  }
  .subActions:has(> *:only-child) {
    flex-direction: column;
  }
  .subActions > * {
    width: 50%;
  }
  .subActions > *:only-child {
    width: auto;
  }
}
@media (min-width: 840px) {
  .subActions > * {
    width: auto;
  }
}
@media (min-width: 1024px) {
  .subActions {
    flex-direction: column;
  }
}

.totalBar {
  position: absolute;
  height: 60px;
}