@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.carousel {
  display: flex;
  align-items: center;
}
.carousel .sliderWrapper {
  position: relative;
  overflow: hidden;
}
.carousel .sliderElements {
  display: flex;
}
.carousel .linearGradient {
  position: absolute;
  z-index: 1;
  width: 80px;
  top: 0;
  height: 100%;
}
@media (max-width: 600px) {
  .carousel .linearGradient {
    display: none;
  }
}
.carousel .linearGradientRight {
  right: 0px;
}
@media (max-width: 600px) {
  .carousel .arrow {
    display: none;
  }
}

.buttonWrapper {
  margin-top: 3rem;
  text-align: center;
}