@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.pricingTable {
  line-height: 2rem;
  width: 100%;
  min-width: 700px;
  border-collapse: collapse;
  position: relative;
}
.pricingTable .pricingFooter {
  display: table-row-group;
}
.pricingTable .currencyColumn {
  width: 14%;
}
.pricingTable .tableHeadRow {
  border-top: solid 1px #8d97a8;
  border-bottom: solid 1px #8d97a8;
  text-align: center;
}
.pricingTable .revenueRow {
  background-color: #c0c6d1;
}
.pricingTable .revenueRowSelected {
  background-color: #94d232;
}
.pricingTable .revenueRow,
.pricingTable .itemizedRow {
  text-align: center;
}
.pricingTable .labelCell {
  text-align: left;
  padding-right: 0;
  padding-left: 1rem;
}
.pricingTable::after {
  content: "";
  display: block;
  position: absolute;
  right: -2.8rem;
  width: 2.5rem;
  height: 1px;
}
.pricingTable .strikethrough {
  text-decoration: line-through;
}