@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.itemWrapper {
  border-bottom: none;
}

.itemWrapperActive.itemWrapperDisabled {
  border-bottom: none;
}

.itemWrapperDisabled {
  pointer-events: none;
}

.itemTitle {
  display: flex;
  width: 100%;
  border-bottom: 1px solid #c0c6d1;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  cursor: pointer;
  user-select: none;
  font-family: "museo-sans", sans-serif;
  color: #0a101a;
  font-size: 16px;
  min-height: 32px;
  font-weight: 700;
  margin-top: 1rem;
  margin-bottom: 2rem;
}
@media (min-width: 600px) {
  .itemTitle {
    font-size: 20px;
  }
}
.itemWrapperActive .itemTitle {
  margin-bottom: 1rem;
}
.itemWrapperDisabled .itemTitle {
  opacity: 0.5;
}