@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.alignCenter {
  text-align: center;
}

.quickLinkOnSidebar {
  font-family: "museo-sans", sans-serif;
  position: relative;
}
@media (min-width: 1024px) {
  .quickLinkOnSidebar {
    display: flex;
  }
}
.quickLinkOnSidebar .sidebarContainer {
  background-color: #e9edf6;
  width: 100%;
  flex-grow: 1;
  position: sticky;
  top: 0;
  margin-bottom: 1rem;
  z-index: 1;
}
@media (min-width: 1024px) {
  .quickLinkOnSidebar .sidebarContainer {
    max-width: 304px;
    padding: 2.5rem;
    padding-right: 1.5rem;
    margin-bottom: 0;
  }
}
.quickLinkOnSidebar .sidebarContainer .desktopStickyContainer {
  position: sticky;
  top: 2.5rem;
}
@media (min-width: 1024px) {
  .quickLinkOnSidebar .sidebarContainer .desktopStickyContainer {
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
}
.quickLinkOnSidebar .sidebarContainer .desktopStickyContainer .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  width: 100%;
}
@media (min-width: 1024px) {
  .quickLinkOnSidebar .sidebarContainer .desktopStickyContainer .header {
    pointer-events: none;
    cursor: default;
  }
}
.quickLinkOnSidebar .sidebarContainer .desktopStickyContainer .header h5 {
  margin: 1rem;
  padding: 0;
  font-weight: 600;
}
@media (min-width: 600px) {
  .quickLinkOnSidebar .sidebarContainer .desktopStickyContainer .header h5 {
    margin-inline: 2rem;
  }
}
@media (min-width: 1024px) {
  .quickLinkOnSidebar .sidebarContainer .desktopStickyContainer .header h5 {
    margin: 0;
  }
}
.quickLinkOnSidebar .sidebarContainer .desktopStickyContainer .header i {
  margin-right: 1rem;
}
@media (min-width: 600px) {
  .quickLinkOnSidebar .sidebarContainer .desktopStickyContainer .header i {
    margin-inline: 2rem;
  }
}
@media (min-width: 1024px) {
  .quickLinkOnSidebar .sidebarContainer .desktopStickyContainer .header i {
    display: none;
  }
}
.quickLinkOnSidebar .sidebarContainer .desktopStickyContainer .links {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  overflow: hidden;
  padding: 2rem;
  padding-top: 1rem;
}
@media (600px < width < 1024px) {
  .quickLinkOnSidebar .sidebarContainer .desktopStickyContainer .links {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(157px, 1fr));
    gap: 1rem;
  }
}
@media (min-width: 1024px) {
  .quickLinkOnSidebar .sidebarContainer .desktopStickyContainer .links {
    padding: 0;
    height: 100% !important;
  }
}
.quickLinkOnSidebar .sidebarContainer .desktopStickyContainer .links .item {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}
.quickLinkOnSidebar .sidebarContainer .desktopStickyContainer .links .item i {
  margin-top: -0.2rem;
}
.quickLinkOnSidebar .sidebarContainer .desktopStickyContainer .links .item a {
  line-height: 1.15;
}
.quickLinkOnSidebar .sidebarContainer .desktopStickyContainer .links .active > a {
  color: #0a101a;
}
.quickLinkOnSidebar .content {
  flex-grow: 1;
}
.quickLinkOnSidebar .content .backToTopButton {
  position: sticky;
  display: flex;
  flex-direction: row-reverse;
  bottom: 0;
}
.quickLinkOnSidebar .content .backToTopButton button {
  padding: 0.5rem;
  background-color: #22b5f0;
  border-radius: 50%;
  margin-bottom: 1.4375rem;
  margin-right: 1.4375rem;
  height: 40px;
}