@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.asLink {
  color: unset;
}
.asLink:hover {
  text-decoration: none;
}

.zoomImageOnHover {
  color: unset;
}
.zoomImageOnHover picture {
  display: block;
  overflow: hidden;
}
.zoomImageOnHover picture img {
  transition: 0.5s;
}
@media (hover: hover) {
  .zoomImageOnHover:hover picture > img {
    transform: scale(1.1);
  }
}

.elementCard {
  background-color: #fff;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  height: 100%;
}
.elementCard img {
  display: block;
}
.elementCardIsFullHeight {
  height: 100%;
}
.elementCardIsFullHeight > *:nth-last-child(2) {
  flex-grow: 1;
}
.elementCard:not(.isHorizontallyAligned) > section, .elementCard:not(.isHorizontallyAligned) > div:not(.horizontalImageContainer, .imageAndVideoWrapper) {
  margin-inline: 1rem;
  width: auto;
}
.elementCard:not(.isHorizontallyAligned) > section:last-child, .elementCard:not(.isHorizontallyAligned) > div:last-child:not(.horizontalImageContainer, .imageAndVideoWrapper) {
  margin-bottom: 1rem;
}
.elementCard.lastItemWithBorderRadius > *:last-child {
  border-bottom-left-radius: var(--border-radius, 4px);
  border-bottom-right-radius: var(--border-radius, 4px);
}

.visibleOverflow {
  overflow: visible;
}

.isHorizontallyAligned {
  display: flex;
}
.isHorizontallyAligned > div:nth-child(2) {
  padding: 1rem;
}

.dropShadow {
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}

.dropShadowOnHover {
  border: none;
  composes: dropShadow;
}
@media (hover: hover) {
  .dropShadowOnHover:hover.dropShadow {
    box-shadow: 8px 8px 10px 0px rgba(0, 0, 0, 0.3);
  }
}

.stroke {
  border: 1px solid #d1d6e0;
  transition: 0.5s;
}

.strokeOnHover {
  box-shadow: none;
  composes: stroke;
}
@media (hover: hover) {
  .strokeOnHover:hover.stroke {
    border: 1px solid #000083;
  }
}

.isHorizontallyAligned {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0;
}

.horizontalImageContainer {
  display: flex;
  align-items: center;
  min-width: 64px;
  max-width: 560px;
  height: 100%;
  overflow: hidden;
  flex-shrink: 0;
}
.horizontalImageContainer * {
  width: 100%;
}

.imageAlignTop {
  align-items: flex-start;
}

.imageAlignBottom {
  align-items: flex-end;
}

.imageContainer {
  background-color: var(--imageContainerBgColor, transparent);
  width: var(--width, auto);
  position: relative;
  height: 100%;
}

.imageAndVideoWrapper {
  overflow: hidden;
}