@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.pagination {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}
@media (max-width: 1024px) {
  .pagination {
    flex-direction: column;
    justify-content: center;
  }
  .pagination .info {
    order: 2;
  }
  .pagination .links {
    order: 1;
  }
}

.paginationFullWidth {
  composes: pagination;
  flex-direction: column;
  justify-content: center;
}
.paginationFullWidth .info {
  order: 2;
}
.paginationFullWidth .links {
  order: 1;
}

.info {
  color: rgba(10, 16, 26, 0.6);
  margin-top: 1rem;
  text-align: center;
}

.links {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.button {
  margin-right: 0.5rem;
}
.button:last-child {
  margin-right: 0;
}

.currentPage {
  font-family: "museo-sans", sans-serif;
  opacity: 0.7;
  text-align: center;
  border: 1px solid #aeb6c2;
  border-radius: 0.125rem;
  width: 2rem;
  height: 2rem;
  margin: 0 0.625rem;
}
.currentPage:focus {
  outline: none;
}