@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.checkbox {
  display: flex;
  overflow: hidden;
}
.checkbox .inputWrapper {
  position: relative;
}
.checkbox input {
  cursor: pointer;
  height: 100%;
  left: 0;
  margin: 0;
  opacity: 0;
  padding: 0;
  position: absolute;
  top: 0;
  z-index: 1;
}
.checkbox input + span {
  display: inline-block;
  height: calc(1rem - 4px);
  width: calc(1rem - 4px);
  border-radius: 2px;
  border: solid 2px #000083;
  background-color: #fff;
  color: #fff;
  margin-right: 0.5rem;
}
.checkbox input + span.noInputMargin {
  margin-right: 0;
}
.checkbox input + span i {
  position: relative;
  left: -2px;
  top: -3px;
}
.checkbox input:disabled + span {
  opacity: 0.5;
}
.checkbox input:checked + span {
  background-color: #000083;
}
.checkbox label {
  cursor: pointer;
  padding: 0.7rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.checkbox label.noLabelPadding {
  padding: 0;
}
.checkbox label:has(:focus-visible) {
  border: 1px solid #000083;
  box-shadow: none;
  outline: none;
}
.checkbox.lightTheme input + span {
  border: solid 2px #22b5f0;
}
.checkbox.lightTheme input:checked + span {
  background-color: #22b5f0;
  color: #000083;
}
.checkbox.lightTheme label {
  color: #fff;
}
.checkbox.disabled label {
  pointer-events: none;
}
.checkbox.disabled input {
  pointer-events: none;
}

.iconWrapper {
  box-sizing: content-box;
}