@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.baseContainer {
  max-width: 1024px;
  padding: 0 2rem;
  box-sizing: border-box;
}
@media (min-width: 1024px) {
  .baseContainer {
    width: 100%;
    margin: 0 auto 2rem;
  }
}
@media (max-width: 480px) {
  .baseContainer {
    padding: 0;
  }
}

.orderDetails p {
  font-size: 1rem;
}
@media (max-width: 840px) {
  .orderDetails p {
    font-size: 0.75rem;
  }
}
@media (max-width: 480px) {
  .orderDetails > * {
    padding: 0 1rem;
  }
}
.orderDetails h3,
.orderDetails .semiBold {
  font-weight: 500;
}
.orderDetails .price {
  font-weight: 900;
}
.orderDetails .infoBar {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  background-color: #e9edf6;
  border: 1px solid #d1d6e0;
  grid-gap: 1rem;
  margin-bottom: 1rem;
  border-radius: 0.125rem;
  padding: 0;
}
.orderDetails .infoBar .infoColumn {
  margin-bottom: 1rem;
  padding: 0 1rem;
}
.orderDetails .infoBar .infoColumn > p:first-child {
  font-weight: 600;
}
.orderDetails .infoBar .infoColumn > p {
  color: rgba(10, 16, 26, 0.7);
}
@media (max-width: 1024px) {
  .orderDetails .infoBar {
    font-size: 14px;
  }
}
@media (max-width: 840px) {
  .orderDetails .infoBar {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 480px) {
  .orderDetails .infoBar {
    grid-template-columns: 1fr 1fr;
    font-size: 0.75rem;
  }
}
.orderDetails .productTitle {
  padding: 0 1rem;
}
.orderDetails .productError {
  background-color: #fdf3f5;
  border: 1px solid #f03e56;
  border-radius: 0.125rem;
  padding: 0;
  margin: 1rem 0;
}
.orderDetails .productError .errorNotification {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  border-bottom: 1px solid #f03e56;
}
.orderDetails .productError .errorNotification > div {
  display: flex;
  align-items: center;
}
.orderDetails .productError .errorNotification i {
  color: #d6223a;
  font-size: 2rem;
  margin-right: 1rem;
}

.row {
  padding: 1rem;
  display: grid;
  align-items: center;
  grid-template-columns: 1fr 1fr;
  border-bottom: 1px solid #d1d6e0;
  border-radius: 0.125rem;
}
@media (max-width: 840px) {
  .row {
    padding: 0 1rem;
    grid-template-columns: 1fr 120px;
    grid-gap: 1rem;
  }
  .row > :nth-child(2) {
    grid-row-start: 2;
    grid-column: 1/-1;
  }
}
.row.error {
  border: none;
}
.row.dark {
  background-color: #e9edf6;
  border: 1px solid #d1d6e0;
}
.row .alignRight {
  text-align: right;
}
.row .priceTotal {
  grid-column-start: 2;
}
.row .priceTotal > div {
  display: flex;
  justify-content: space-between;
}
.row .priceTotal > div :nth-child(2) {
  text-align: right;
}
@media (max-width: 840px) {
  .row .priceTotal {
    grid-column: 1/-1;
  }
}
.row .info {
  display: grid;
  align-items: center;
  grid-template-columns: auto 1fr 70px 1fr;
  grid-gap: 1.5rem;
}
@media (max-width: 1024px) {
  .row .info {
    grid-template-columns: 1fr 60px 1fr;
  }
}
@media (max-width: 840px) {
  .row .info {
    grid-template-columns: auto 1fr 60px 1fr;
  }
}

.downloadEbook {
  margin-right: 1rem;
}
@media (max-width: 600px) {
  .downloadEbook {
    margin-right: 0;
  }
}

.header {
  margin-top: 2rem;
  margin-bottom: 1rem;
}

.headerContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 600px) {
  .headerContainer {
    display: block;
  }
}

@media (max-width: 840px) {
  .containerMultipleButtons {
    display: block;
  }
}

.status {
  display: flex;
  align-items: center;
}
.status > div {
  margin-left: 1rem;
}
.status > div i {
  font-size: 2rem;
}

.orderItemProduct {
  align-items: center;
  display: flex;
  word-break: break-word;
}
.orderItemProduct.title {
  padding: 0 1rem;
}
.orderItemProduct.error {
  padding-left: 1rem;
}
@media (max-width: 840px) {
  .orderItemProduct.error {
    padding-left: 0;
  }
}

.productThumbnail {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #fff;
  min-width: 4rem;
  max-width: 4rem;
  height: 4rem;
  border-radius: 0.125rem;
  border: 1px solid #d1d6e0;
  position: relative;
}
.productThumbnail img {
  max-height: 100%;
  max-width: 100%;
}
.productThumbnail .noCover {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
  font-weight: 700;
  line-height: 1.25rem;
  text-transform: uppercase;
  color: #aeb6c2;
  height: 100%;
  width: 100%;
}

.productIcon {
  font-size: 1.5rem;
  color: #8d97a8;
}
@media (max-width: 1024px) {
  .productIcon {
    display: none;
  }
}
@media (max-width: 840px) {
  .productIcon {
    display: block;
  }
}
@media (max-width: 480px) {
  .productIcon {
    font-size: 1rem;
  }
}

.actionsContainer {
  align-items: center;
  display: flex;
  justify-content: flex-end;
}
.actionsContainer > button {
  margin-right: 1rem;
}
.actionsContainer > button:last-child {
  margin-right: 0;
}

.orderActions {
  display: flex;
  justify-content: flex-end;
  margin: 1rem 0;
}

.goBack {
  display: flex;
  align-items: center;
  width: fit-content;
  font-family: "museo-sans", sans-serif;
}
.goBack i {
  font-size: 1.5rem;
  margin-right: 0.5rem;
}
.goBack:hover {
  text-decoration: none;
}
.goBack:hover span {
  text-decoration: underline;
}