@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.react-datepicker {
  background-color: #fff;
  border: 1px solid #d7dbe2;
  border-radius: 2px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  display: inline-block;
  font-family: "museo-sans", sans-serif;
  padding: 1rem;
  position: relative;
}
.react-datepicker .react-datepicker__aria-live {
  display: none;
}
.react-datepicker-popper {
  position: absolute;
  will-change: transform;
  top: 0;
  left: 0;
  transform: translate3d(334px, 250px, 0);
  margin-top: 10px;
  z-index: 1;
}
.react-datepicker__triangle {
  display: none;
}
.react-datepicker__header {
  background-color: #fff;
  border: none;
  padding: 0;
}
.react-datepicker__day-name {
  color: rgba(10, 16, 26, 0.6);
  cursor: default;
  display: inline-block;
  font-size: 10px;
  font-stretch: normal;
  font-style: normal;
  font-weight: 500;
  letter-spacing: normal;
  line-height: 1;
  margin: 0.166rem;
  text-align: center;
  width: 1.7rem;
}
.react-datepicker__day-names, .react-datepicker__week {
  white-space: nowrap;
}
.react-datepicker__day {
  color: #000083;
  cursor: pointer;
  display: inline-block;
  font-size: 10px;
  font-stretch: normal;
  font-style: normal;
  font-weight: 500;
  letter-spacing: normal;
  line-height: 1.7rem;
  margin: 0.166rem;
  text-align: center;
  width: 1.7rem;
}
.react-datepicker__day--outside-month {
  visibility: hidden;
}
.react-datepicker__day--disabled {
  color: #aeb6c2;
  cursor: default;
}
.react-datepicker__day--disabled:hover, .react-datepicker__day--disabled:focus, .react-datepicker__day--disabled:active {
  color: #aeb6c2;
  background-color: #fff !important;
  font-weight: 500 !important;
}
.react-datepicker__day--selected, .react-datepicker__day--keyboard-selected, .react-datepicker__day:hover, .react-datepicker__day:focus, .react-datepicker__day:active {
  background-color: #22b5f0;
  border-radius: 50%;
  font-weight: 900;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.7rem;
  letter-spacing: normal;
  outline: none;
  text-align: center;
}
.react-datepicker__month-text {
  background-color: #dce0eb;
  border-radius: 2px;
  box-sizing: border-box;
  color: #000083;
  cursor: pointer;
  font-size: 1rem;
  font-stretch: normal;
  font-style: normal;
  font-weight: 500;
  line-height: 1;
  letter-spacing: normal;
  padding: 0.5rem 1.5rem;
  text-align: center;
  width: 72px !important;
}
.react-datepicker__month-text:hover, .react-datepicker__month-text:active, .react-datepicker__month-text:focus {
  border-radius: 2px;
  background-color: #22b5f0;
}
.react-datepicker__month {
  margin: 0;
}
.react-datepicker__month--selected {
  background-color: #22b5f0;
}
.react-datepicker__month--disabled {
  color: #fff;
  background-color: #e9edf6;
  cursor: default;
}
.react-datepicker__month--disabled:hover, .react-datepicker__month--disabled:focus, .react-datepicker__month--disabled:active {
  color: #fff;
  background-color: #e9edf6;
}
.react-datepicker__month-wrapper {
  display: flex;
}
.react-datepicker__month-wrapper > * {
  margin: 0 1rem 1rem 0;
}
.react-datepicker__month-wrapper > *:last-child {
  margin-right: 0;
}
.react-datepicker__month-wrapper:last-child > * {
  margin-bottom: 0;
}