@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.dropDownContainer {
  position: relative;
  background-color: #000083;
  color: #fff;
  border-radius: 0.125rem;
  padding: 0 1rem;
  display: flex;
  align-items: center;
}
.dropDownContainer label {
  color: #fff;
}
.dropDownContainer.increasedHorizontalPadding {
  padding: 0 3rem;
}
.dropDownContainer.withArrow {
  margin-bottom: 1rem;
}
.dropDownContainer.withArrow::after, .dropDownContainer.withArrow::before {
  bottom: -2rem;
  left: 50%;
  border: solid transparent;
  content: "";
  height: 0;
  width: 0;
  position: absolute;
  z-index: 2;
}
.dropDownContainer.withArrow::after {
  border-top-color: #000083;
  border-width: 1rem;
  margin-left: -1rem;
}
.dropDownContainer .title {
  margin: 0;
  flex: 0.3;
  min-width: 120px;
  padding-right: 1rem;
}
.dropDownContainer .fieldWrapper {
  flex: 0.5;
}
.dropDownContainer .fieldWrapper :global .field {
  padding: 1rem 0 !important;
}
.dropDownContainer .fieldWrapper :global .input-icon {
  top: 47%;
}
@media (max-width: 840px) {
  .dropDownContainer {
    display: block;
  }
  .dropDownContainer .title {
    padding-top: 1rem;
  }
}