@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.nextStep {
  --color: #22b5f0;
  --color-light: #3dc8ff;
  --button-font-weight: 900;
}
@media (max-width: 1024px) {
  .nextStep {
    margin: 0 2rem;
  }
}
@media (max-width: 600px) {
  .nextStep {
    margin: 0 1.5rem;
  }
}
@media (max-width: 480px) {
  .nextStep {
    height: 40px;
    margin: 0 1rem;
  }
}
.nextStep h2 {
  font-weight: 300;
  margin-top: 0;
  margin-bottom: 1rem;
  padding: 0 2.5rem;
}
.nextStep button {
  width: 100%;
  line-height: 1.17;
  font-weight: var(--button-font-weight);
  padding: 1.125rem 0.25rem;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
@media (min-width: 840px) {
  .nextStep button {
    font-size: 1.5rem !important;
  }
}
@media (max-width: 840px) {
  .nextStep button {
    height: 56px;
  }
}
@media (max-width: 480px) {
  .nextStep button {
    height: 40px;
  }
}
.nextStep button span {
  justify-content: center;
}
.nextStep button:disabled {
  pointer-events: none;
}
.nextStep button:disabled::after {
  border-left-color: var(--color-light);
}
.nextStep button:hover::after, .nextStep button:focus::after {
  transition: 0.3s;
  border-left-color: var(--color-light);
}

.primary {
  --color: #000083;
  --color-light: #2424b5;
  --button-font-weight: 600;
}
.primary :disabled {
  --color-light: #000083;
}

.buttonWrapper {
  margin-bottom: 1rem;
  clip-path: polygon(0 0, 59% 0, 96% 0%, 100% 50%, 96% 100%, 60% 100%, 0 100%);
}