@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container {
  margin-bottom: 2.5rem;
}

.selectors {
  display: flex;
  gap: 1rem;
  width: 100%;
}
@media (min-width: 600px) {
  .selectors {
    width: 50%;
  }
}

.calendarSizeContainer {
  border: 1px solid #aeb6c2;
  border-radius: 2px;
  position: relative;
  display: flex;
  margin-top: 1.5rem;
}

.calendarSizeValue {
  padding: 1rem;
}

.calendarSizeLabel {
  color: rgba(10, 16, 26, 0.6);
  position: absolute;
  pointer-events: none;
  left: 0.5rem;
  top: -0.375rem;
  transition: 0.2s ease all;
  text-transform: none;
  font-size: 0.75rem;
  font-weight: 500;
  letter-spacing: unset;
  background-color: #fff;
  padding-inline: 0.5rem;
}

.errorContainer {
  padding-right: 1rem;
}