@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.wrapper {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1rem 0;
  font-family: "museo-sans", sans-serif;
  gap: 1rem;
}
@media (min-width: 480px) {
  .wrapper {
    border-bottom: 1px solid #d1d6e0;
  }
}
.wrapper .productInfo {
  min-width: 190px;
  width: 100%;
  display: grid;
  grid-template-columns: 4.125rem 1fr;
  gap: 0.5rem;
  grid-template-areas: "thumbnail any" "thumbnail any" "thumbnail any";
  align-items: center;
}
.wrapper .productInfo .productThumbnail {
  grid-area: thumbnail;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 4.125rem;
  height: 4.125rem;
  position: relative;
  padding-right: 4px;
}
.wrapper .productInfo .productThumbnail a {
  text-decoration: none;
  color: #aeb6c2;
  display: flex;
  align-items: center;
}
.wrapper .productInfo .productThumbnail a img {
  max-height: 4.125rem;
  max-width: 4.125rem;
  width: 100%;
}
.wrapper .productInfo .productThumbnail .noCover {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
  font-weight: 700;
  line-height: 1.25rem;
  text-transform: uppercase;
  color: #aeb6c2;
  height: 100%;
  width: 100%;
}
.wrapper .productInfo .productName,
.wrapper .productInfo .productUnitPrice {
  font-size: 0.875rem;
  line-height: 135%;
}
.wrapper .productInfo .productName {
  font-weight: 700;
}
.wrapper .productInfo .productName a {
  text-decoration: none;
  color: #0a101a;
}
.wrapper .productInfo .productUnitPrice {
  line-height: 150%;
  font-weight: 500;
}
.wrapper .productInfo .productUnitPrice .saleIcon {
  line-height: 125%;
  margin-top: 0.5625rem;
  margin-bottom: 0.0625rem;
}
.wrapper .productInfo .productUnitPrice .saleIcon span {
  position: relative;
  top: 0.125rem;
}
.wrapper .productInfo .productUnitPrice .bulkDiscount {
  color: rgba(10, 16, 26, 0.6);
  font-weight: 700;
  line-height: 133%;
}
.wrapper .productInfo .productType {
  color: rgba(10, 16, 26, 0.6);
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  font-size: 0.75rem;
  align-items: center;
}
.wrapper .productInfoLoading {
  opacity: 0.4;
}
.wrapper .quantityAndPrice {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-width: 68px;
  max-width: 120px;
}
.wrapper .quantityAndPrice .removeButton {
  text-align: right;
}
.wrapper .quantityAndPrice .removeButton button {
  padding: 0;
}
.wrapper .quantityAndPrice .removeButton button:focus i::before, .wrapper .quantityAndPrice .removeButton button:hover i::before, .wrapper .quantityAndPrice .removeButton button:active i::before {
  color: #000083;
}
.wrapper .quantityAndPrice .total,
.wrapper .quantityAndPrice .quantity {
  font-size: 0.875rem;
  font-weight: 700;
  line-height: 125%;
  text-align: right;
  white-space: nowrap;
}
.wrapper .quantityAndPrice .total s,
.wrapper .quantityAndPrice .quantity s {
  font-weight: 400;
  color: rgba(10, 16, 26, 0.6);
}
.wrapper .quantityAndPrice .quantity {
  font-weight: 500;
}
.wrapper .quantityAndPrice .quantityPickerWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.wrapper .quantityAndPrice .quantityPicker {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 0.75rem;
  line-height: 1rem;
  margin-bottom: 0.5rem;
  border: 1px solid #aeb6c2;
  border-radius: 1px;
  background: #fff;
}
.wrapper .quantityAndPrice .quantityPicker:focus, .wrapper .quantityAndPrice .quantityPicker:hover, .wrapper .quantityAndPrice .quantityPicker:active, .wrapper .quantityAndPrice .quantityPicker:focus-within {
  border-color: #000083;
}
.wrapper .quantityAndPrice .quantityPicker .button {
  display: inline;
  cursor: pointer;
}
.wrapper .quantityAndPrice .quantityPicker .button:focus::before, .wrapper .quantityAndPrice .quantityPicker .button:hover::before, .wrapper .quantityAndPrice .quantityPicker .button:active::before {
  color: #000083;
}
.wrapper .quantityAndPrice .quantityPicker input,
.wrapper .quantityAndPrice .quantityPicker span {
  font-size: 0.875rem;
}
.wrapper .quantityAndPrice .quantityPicker input {
  padding: 2px;
  width: 30px;
  height: 22px;
  text-align: center;
  border: none;
}
.wrapper .quantityAndPrice .quantityPicker input:focus, .wrapper .quantityAndPrice .quantityPicker input:hover, .wrapper .quantityAndPrice .quantityPicker input:active {
  color: #000;
  outline: none;
}
.wrapper .quantityAndPriceLoading {
  opacity: 0.4;
}
.wrapper .loader {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  display: flex;
  align-items: center;
}
.wrapper .loader > div {
  width: 22px;
  height: 22px;
}
.wrapper .loader > div div {
  width: 22px;
  height: 22px;
  border-width: 4px;
}

.removedItem {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  justify-content: center;
  align-items: center;
  padding: 2.3125rem 0;
}
@media (min-width: 480px) {
  .removedItem {
    border-bottom: 1px solid #d1d6e0;
  }
}