@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container {
  border-bottom: 1px solid #d1d6e0;
  margin-bottom: 1rem;
}

.title {
  margin-bottom: 0.5rem;
  margin-top: 0;
  font-weight: 600;
  font-size: 1rem;
}
@media (min-width: 600px) {
  .title {
    font-size: 1.25rem;
  }
}