@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.statusInfo {
  align-items: center;
  display: flex;
  margin: 0.5rem 0;
}
.statusInfo.hasNoMargin {
  margin: 0;
}
.statusInfo .iconDescriptor {
  font-family: "museo-sans", sans-serif;
  font-size: 0.625rem;
  font-weight: 900;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  color: #0a101a;
}
.statusInfo .iconDescriptor.retired {
  color: rgba(10, 16, 26, 0.6);
}
@media (max-width: 600px) {
  .statusInfo {
    flex-flow: row;
    justify-content: center;
  }
}
.statusInfo .productShareLink {
  display: flex;
  align-items: center;
}
.statusInfo .productShareLink:hover {
  cursor: pointer;
  text-decoration: none;
}