@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.projectCard {
  display: flex;
  gap: 1rem;
  background-color: #f2f4fa;
  border: 1px solid #dce0eb;
  border-radius: 0.125rem;
  padding: 1rem;
  flex-direction: column;
}
@media (min-width: 600px) {
  .projectCard {
    flex-direction: row;
  }
}

.whiteBackground {
  background-color: #fff;
}

.projectThumbnail {
  align-self: center;
}
@media (max-width: 600px) {
  .projectThumbnail {
    margin: auto;
  }
}

.projectTitle {
  align-items: center;
  gap: 0.3rem;
  margin: 0;
  margin-bottom: 0.4rem;
  font-weight: 600;
  overflow-wrap: anywhere;
}
@media (max-width: 1024px) {
  .projectTitle {
    font-size: 1.25rem;
  }
}
@media (max-width: 600px) {
  .projectTitle {
    font-size: 1rem;
  }
}

.detailsLeftCol {
  margin-right: auto;
  display: flex;
  flex-direction: column;
  flex: 1;
}

.projectLink {
  position: relative;
  top: 5px;
}
@media (max-width: 600px) {
  .projectLink {
    top: 6px;
  }
}

.detailRow {
  color: rgba(10, 16, 26, 0.6);
  margin-top: 0.3rem;
  line-height: 140%;
}
.detailRow:first-of-type {
  margin-top: 0;
}
.detailRow span {
  color: #0a101a;
  font-weight: 600;
}

.contentContainer {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  gap: 1rem;
  font-family: "museo-sans", sans-serif;
}
@media (min-width: 600px) {
  .contentContainer {
    flex-direction: row;
    gap: 2rem;
  }
}

.statusContainer {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  flex: 0.5;
}
@media (min-width: 600px) {
  .statusContainer {
    justify-content: flex-end;
    margin-right: 1rem;
  }
}

.detailsRightCol {
  line-height: 150%;
  display: flex;
  flex-direction: column;
  flex: 0.9;
}
@media (min-width: 600px) {
  .detailsRightCol {
    padding-top: 0.5rem;
  }
}

.projectType {
  color: #0a101a;
  font-weight: 600;
}

.podPackageDetails {
  font-weight: 300;
}

.optionValue {
  font-weight: 300;
  color: #0a101a;
}