@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.subheading {
  composes: subhead from global;
  margin-bottom: 1.5rem;
}
@media (max-width: 1024px) {
  .subheading {
    margin-bottom: 1rem;
  }
}

.downloadFieldContainer {
  display: flex;
  justify-content: center;
}

.downloadButton {
  composes: button from global;
}

.downloadField {
  composes: field from global;
  align-items: flex-end;
  flex-wrap: nowrap;
  width: 50%;
}
.downloadField > div {
  flex-grow: 1;
}
.downloadField .downloadButton {
  font-size: 0.75rem;
  padding: 1.25rem;
  max-height: 51px;
}
@media (max-width: 1024px) {
  .downloadField {
    width: 75%;
  }
}
@media (max-width: 600px) {
  .downloadField {
    width: 100%;
    flex-wrap: wrap;
    align-items: stretch;
    justify-content: center;
  }
  .downloadField .downloadButton {
    height: 51px;
    margin-top: 1rem;
    border-top-left-radius: 0.125rem;
    border-bottom-left-radius: 0.125rem;
  }
}