@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container {
  position: relative;
}

.content {
  background-color: #e9edf6;
  border: 0.0625rem solid #d1d6e0;
  border-radius: 0.125rem;
  padding-left: 3rem;
  padding-right: 5rem;
  display: flex;
  overflow-x: scroll;
  scrollbar-width: none;
  line-height: normal;
}
.content::-webkit-scrollbar {
  display: none;
}

.active::before {
  content: "";
  display: block;
  position: absolute;
  width: 0.5rem;
  height: calc(100% - 2px);
  background-color: #94d232;
  left: 1px;
  top: 1px;
  border-top-left-radius: 0.125rem;
  border-bottom-left-radius: 0.125rem;
  z-index: 1;
}
.active .content {
  background-color: #fff;
  border-color: #000083;
}
.active .title {
  margin-top: 2rem;
}
.active .reference {
  margin-top: 2rem;
}

.text {
  flex: 0.3;
  margin: 1rem 0;
  min-width: fit-content;
  padding-right: 1rem;
  justify-content: space-between;
  display: flex;
  flex-direction: column;
}
.text .title {
  color: #000083;
  font-weight: bold;
}
.text .reference {
  display: block;
  font-family: "museo-sans", sans-serif;
  font-weight: 300;
}
.text .reference div + div {
  margin-top: 0.38rem;
}
.text .reference--read-only {
  margin-top: 1rem;
}

.form {
  flex: 1;
  background-color: #e9edf6;
}

.link {
  font-size: 0.75rem;
  margin-left: 2rem;
  display: inline-block;
  margin-top: 0.5rem;
  text-decoration: underline;
  font-family: "interface", sans-serif;
  font-weight: 300;
}

.info {
  color: rgba(10, 16, 26, 0.6);
  font-family: "interface", sans-serif;
  font-size: 0.75rem;
  line-height: 1rem;
  position: relative;
  margin-top: 0.5rem;
  margin-left: 2rem;
}
.info::before {
  content: "*";
  position: absolute;
  top: 0;
  left: -0.5rem;
}

.invalid::before {
  background-color: #bd0b23;
}

@media (max-width: 1024px) {
  .content {
    padding-left: 1.3rem;
  }
}