@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.content {
  background-color: #e9edf6;
  padding: 1rem;
}
@media (min-width: 840px) {
  .content {
    padding: 2rem;
  }
}

.checkboxWrapper {
  margin: 1rem 0;
}

.text {
  font-family: "museo-sans", sans-serif;
  font-size: 0.875rem;
  font-weight: 300;
  margin: 0;
}
@media (min-width: 600px) {
  .text {
    font-size: 1rem;
  }
}

.contentHeader > * {
  margin-bottom: 1.5rem;
}

.minimumListPrice {
  font-weight: 400;
  margin-bottom: 0.5rem;
  line-height: 1.5;
}

@media (min-width: 600px) {
  .listPriceInput {
    width: 232px;
  }
}

.revenueTableWrapper {
  margin-top: 1.5rem;
}

.price {
  margin-left: 8px;
  font-weight: 600;
}

.footer {
  font-family: "museo-sans", sans-serif;
  font-size: 0.875rem;
  font-weight: 300;
  color: rgba(10, 16, 26, 0.7);
}

.footer a {
  text-decoration: underline;
}

.panelSelection {
  display: flex;
  align-items: stretch;
}

.panelItem {
  flex: 1;
  text-align: center;
  border-bottom: 2px solid transparent;
  padding: 1rem 0;
  font-family: "museo-sans", sans-serif;
  font-size: 0.75rem;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 600px) {
  .panelItem {
    font-size: 1.25rem;
  }
}
.panelItem:hover {
  cursor: pointer;
}

.activePanelItem {
  color: #000083;
  border-color: #000083;
}

.description {
  margin-bottom: 1.5rem;
}

.globalDistribution {
  margin-left: -0.5rem;
  font-size: 0.875rem;
}
@media (min-width: 600px) {
  .globalDistribution {
    font-size: 1rem;
  }
}