@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.selector {
  position: relative;
  margin-bottom: 2rem;
  margin-inline: -1rem;
}
@media (min-width: 480px) {
  .selector {
    margin-inline: -1.5rem;
  }
}
@media (min-width: 600px) {
  .selector {
    margin-inline: -1rem;
  }
}
@media (min-width: 840px) {
  .selector {
    margin-inline: 0;
  }
}

.optionContainer {
  display: flex;
  overflow-x: auto;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch; /* Momentum scrolling iOS */
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
@media (max-width: 1024px) {
  .optionContainer {
    padding: 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    overflow: hidden;
  }
}
@media (max-width: 840px) {
  .optionContainer {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 600px) {
  .optionContainer {
    display: flex;
    flex-wrap: nowrap;
    overflow: auto;
    padding: 0.8rem;
    background-color: #f2f4fa;
    border-bottom: 1px solid #d1d6e0;
    border-top: 1px solid #d1d6e0;
  }
}
.optionContainer::-webkit-scrollbar {
  display: none;
}

.radioContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.5rem;
  border: 1px solid transparent;
  border-radius: 0.125rem;
  cursor: pointer;
}
@media (min-width: 1024px) {
  .radioContainer {
    padding: 0.9rem;
  }
  .radioContainer:hover {
    border: 1px solid #000083;
  }
}

@media (min-width: 1024px) {
  .option:first-child {
    margin-left: auto;
  }
  .option:last-child {
    margin-right: auto;
  }
}

.active {
  background-color: #fff;
  border-color: #000083;
  border-radius: 0.125rem;
  pointer-events: none;
}
@media (min-width: 600px) {
  .active {
    background-color: #f2f4fa;
  }
}
@media (min-width: 1024px) {
  .active {
    border-color: transparent;
    border-bottom-color: #000083;
  }
  .active:hover {
    border-color: transparent;
    border-bottom-color: #000083;
  }
}

.imageContainer {
  width: 155px;
  height: 87px;
}
@media (max-width: 1440px) {
  .imageContainer {
    width: 114px;
    height: 57px;
  }
}
@media (max-width: 1024px) {
  .imageContainer {
    width: 162px;
    height: 90px;
  }
}
@media (max-width: 600px) {
  .imageContainer {
    width: 121px;
    height: 68px;
  }
}

.image {
  width: 100%;
  height: 100%;
}

.label {
  margin-top: 0.5rem;
  margin-bottom: 0;
  font-weight: 700;
  font-size: 1rem;
}
@media (max-width: 600px) {
  .label {
    font-size: 0.75rem;
  }
}

.indicator {
  position: relative;
  margin-top: 15px;
}
@media (max-width: 1024px) {
  .indicator {
    display: none;
  }
}

.indicator:after,
.indicator:before {
  bottom: 100%;
  left: 50%;
  border: solid transparent;
  content: "";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  top: -16px;
}

.indicator:after {
  border-color: transparent;
  border-top-color: #f2f4fa;
  border-width: 15px;
  margin-left: -15px;
}

.indicator:before {
  border-color: transparent;
  border-top-color: #000083;
  border-width: 16px;
  margin-left: -16px;
}

.radioBorderSelection {
  z-index: -1;
  position: relative;
  top: -16px;
  width: 100%;
  height: 1px;
  background-color: #d1d6e0;
}
@media (max-width: 1024px) {
  .radioBorderSelection {
    width: 100%;
    left: 0;
    top: 0;
  }
}
@media (max-width: 600px) {
  .radioBorderSelection {
    display: none;
  }
}

.caret {
  position: absolute;
  top: 35%;
  opacity: 0;
  transition: opacity 0.3s;
}
@media (min-width: 600px) {
  .caret {
    display: none;
  }
}

.caretLeft {
  left: 0.2rem;
  rotate: 90deg;
}

.caretRight {
  right: 0.2rem;
  rotate: 270deg;
}

.gradient {
  position: absolute;
  top: 0;
  max-height: 100%;
  opacity: 0;
  transition: opacity 0.3s;
}
@media (min-width: 600px) {
  .gradient {
    display: none;
  }
}

.displayElement {
  opacity: 100;
}

.gradientLeft {
  rotate: 180deg;
  left: 0;
}

.gradientRight {
  right: 0;
}