@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.selectContainer {
  display: flex;
  flex-direction: column;
  margin-block: 1rem;
  gap: 1rem;
}

.selectMenuHeader {
  display: flex;
  justify-content: space-between;
  font-family: "museo-sans", sans-serif;
  font-size: 0.75rem;
  padding: 0.5rem 1rem;
}