@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.breadcrumbsContainer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  color: #000083;
  font-family: "museo-sans", sans-serif;
  font-size: 0.75rem;
  line-height: 1rem;
}

.breadcrumb {
  width: max-content;
}

.link:hover {
  color: #2424b5;
}

.slash {
  margin: 0 0.25rem;
}

.currentPage {
  font-weight: 700;
}