@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.wrapper {
  width: auto;
}

.wrapperDisabled {
  background-color: #e9edf6;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 0.5rem;
}

.boxContentWrapper {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;
  padding-inline: 5%;
  height: 100%;
}

.emptyBox,
.loadingBox {
  background-color: rgba(34, 181, 240, 0.1);
  border-radius: 2px;
  border: 2px dashed #22b5f0;
  box-sizing: border-box;
  cursor: pointer;
  position: relative;
  height: 100%;
}
.emptyBox:hover,
.loadingBox:hover {
  background-color: rgba(34, 181, 240, 0.05);
}

.withError {
  border-left: 4px solid #bd0b23;
}

.boxContent {
  line-height: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 0.45rem;
  font-family: "museo-sans", sans-serif;
}
.boxContent span {
  color: rgba(10, 16, 26, 0.6);
  font-weight: 300;
}
.boxContent i {
  font-size: 1.65rem;
}
.boxContent .textIndigoUnderline {
  color: #000083;
  text-decoration: underline;
  font-size: 0.875rem;
  font-weight: 400;
}
.boxContent .textDragAndDrop {
  font-size: 0.875rem;
  font-weight: 400;
}
@media (max-width: 480px) {
  .boxContent {
    font-size: 0.75rem;
  }
  .boxContent i {
    font-size: 1.5rem;
  }
}

.draggable {
  cursor: grab;
}

.dropzone {
  height: 100%;
}
.dropzone:focus {
  outline: none;
}

.uploadedBox {
  display: flex;
  position: relative;
  height: 100%;
}
@media (max-width: 480px) {
  .uploadedBox {
    width: auto;
  }
}
.uploadedBox:hover .cta {
  display: block;
}
.uploadedBox:hover .image-upload-box__delete {
  border-top: 1px solid rgba(209, 214, 224, 0.3);
  border-left: 1px solid rgba(209, 214, 224, 0.3);
}
.uploadedBox .cornerActionWrapper:hover ~ .cta {
  display: none;
}

.dragIcon {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: #fff;
  background-color: #000083;
  width: 2rem;
  border: 1px solid #000083;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
}
@media (max-width: 480px) {
  .dragIcon {
    font-size: 1.5rem;
    width: 1.5rem;
  }
}

.image {
  background-color: #fff;
  background-position: center;
  background-repeat: no-repeat;
  border: 1px solid #d1d6e0;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
  width: 100%;
}

.imageWithoutCornerAction {
  border-color: #f5f4f2;
}

.errorInfo {
  background-color: #bd0b23;
  border: 1px solid #bd0b23;
  top: -2px;
  color: #fff;
  cursor: pointer;
  font-size: 1.5rem;
  padding: 2px;
  position: absolute;
  left: -3px;
}
@media (max-width: 480px) {
  .errorInfo {
    font-size: 1rem;
  }
}

.uploadingText {
  margin-bottom: 0.5rem;
  font-size: 0.875rem;
  font-weight: 400;
}

.uploadTextVertical {
  font-size: 0.875rem;
  font-weight: 400;
}

.loadingBar {
  height: 6px;
  border-radius: 0.125rem;
  background-color: #22b5f0;
  width: 100%;
  position: relative;
  overflow: hidden;
}
@media (max-width: 480px) {
  .loadingBar {
    margin-bottom: 0.5rem;
    width: 75%;
  }
}

.loadingBarContent {
  display: block;
  position: absolute;
  height: 6px;
  border-radius: 0.125rem;
  background-color: #000083;
  top: 0;
  right: 100%;
  bottom: 0;
  left: 0;
  width: 0;
  animation: progressBar 3s linear infinite;
}

.deleting {
  right: 0;
  padding: 0;
}
.deleting span {
  display: inline-block;
  height: 22px;
  position: relative;
  width: 24px;
}
.deleting i {
  animation: rotating 1s linear infinite;
  position: absolute;
}

.delete,
.deleting {
  background-color: #fff;
  border-radius: 2px;
  border-top: 1px solid #d1d6e0;
  border-left: 1px solid #d1d6e0;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
  border-top-right-radius: 0;
  border-bottom-left-radius: 0;
  bottom: 0;
  color: #000083;
  font-size: 1.5rem;
  padding: 0.5rem 1px 1px 0.5rem;
  position: absolute;
  right: 0;
}
@media (max-width: 480px) {
  .delete,
  .deleting {
    font-size: 1.25rem;
    padding-top: 0.35rem;
    padding-left: 0.35rem;
  }
}

.deleting {
  cursor: auto;
}

.delete {
  cursor: pointer;
}
.delete:hover {
  color: rgba(0, 0, 131, 0.7);
}

@keyframes progressBar {
  0% {
    left: 0;
    right: 100%;
    width: 0;
  }
  10% {
    left: 0;
    right: 75%;
    width: 25%;
  }
  90% {
    right: 0;
    left: 75%;
    width: 25%;
  }
  100% {
    left: 100%;
    right: 5%;
    width: 0;
  }
}
.disableCopy {
  font-family: "museo-sans", sans-serif;
  font-weight: 400;
  font-size: 0.875rem;
  color: rgba(10, 16, 26, 0.6);
  text-align: center;
  padding-inline: 0.25rem;
}

.checkbox {
  position: absolute;
  display: flex;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  right: 1px;
  bottom: 1px;
  height: 32px;
  width: 32px;
  border-top-left-radius: 0.125rem;
  border-style: solid;
  border-color: #d1d6e0;
  border-width: 1px 0 0 1px;
}

.errorMessage {
  font-family: "museo-sans", sans-serif;
  font-size: 0.75rem;
  color: #bd0b23;
}

.cta {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
}

.ctaButton {
  border: 1px solid #d1d6e0;
}

.ctaTitle {
  font-family: "museo-sans", sans-serif;
  font-size: 0.625rem;
  font-weight: 600;
}