@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.checkbox {
  position: relative;
  background-color: #dce0eb;
  border: 1px solid #c0c6d1;
  border-radius: 0.125rem;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.checkbox.modern {
  background-color: #fff;
}
.checkbox.checked {
  background-color: #fff;
  border-color: #000083;
}
.checkbox.checked::before {
  content: "";
  display: block;
  position: absolute;
  width: 7px;
  height: 100%;
  left: 0;
  background-color: #94d232;
}
.checkbox.disabled {
  cursor: default;
}
.checkbox.disabled.modern {
  background-color: #dce0eb;
}
.checkbox .checkboxWrapper {
  margin-left: 1rem;
}
.checkbox .checkboxWrapper label {
  padding: 0;
}

.content {
  padding: 1rem;
}

.title {
  font-family: "museo-sans", sans-serif;
  font-weight: bold;
}
.title.indigo {
  color: #000083;
}
.title.black {
  color: #0a101a;
}
.disabled .title {
  color: rgba(10, 16, 26, 0.6);
}

.description {
  color: rgba(10, 16, 26, 0.7);
  margin: 0;
  margin-top: 0.25rem;
}
.modern .description {
  font-family: "museo-sans", sans-serif;
  font-weight: 300;
  color: rgba(10, 16, 26, 0.6);
  line-height: 1.5;
  margin-top: 0.7rem;
}
.modern.disabled .description {
  color: rgba(10, 16, 26, 0.6);
}