@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.card {
  background-color: #f2f4fa;
  padding: 0.4rem;
  border: 1px solid #c0c6d1;
  border-radius: 0.125rem;
}

.inputBtnContainer {
  flex-wrap: nowrap;
  padding: 0.7rem;
  padding-top: 0.4rem;
}

.addedNumber {
  margin: 0.7rem;
  margin-top: 0.5rem;
  margin-left: 2.6rem;
  font-family: "museo-sans", sans-serif;
  word-break: break-all;
}