@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.videoContainer {
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
}
.videoContainer iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}

.playButtonWrapper {
  position: relative;
  display: inline-block;
  width: 100%;
  cursor: pointer;
}
.playButtonWrapper_playButton {
  position: absolute;
  text-align: center;
  width: 60px;
  height: 40px;
  background-color: #000083;
  opacity: 0.7;
  border-radius: 0.125rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.playButtonWrapper_playButton:hover {
  background-color: #000083;
}
.playButtonWrapper_playButton:active {
  background-color: #2424b5;
}
.playButtonWrapper_playButton svg {
  position: relative;
  width: 24px;
  height: 24px;
  top: 18%;
}
.playButtonWrapper_playButton svg path {
  fill: #fff;
}
.playButtonWrapper img {
  width: 100%;
}

.videoPlayer {
  width: 100%;
}

.gCloudVideo {
  width: 100%;
  height: 100%;
  object-fit: cover;
}