@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.backdrop {
  background-color: rgba(10, 16, 26, 0.4);
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 12;
}

.dialog {
  box-sizing: border-box;
  max-height: 95vh;
  background-color: #fff;
  border: 0.0625rem solid #d7dbe2;
  box-shadow: 0 0 32px 0 rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  left: 50%;
  max-width: 90%;
  min-width: 448px;
  overflow-y: auto;
  padding: 1rem;
  padding-top: 3rem;
  padding-bottom: 2rem;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}
@media (max-width: 480px) {
  .dialog {
    max-width: 90%;
    min-width: 66%;
  }
}

.widerDialog {
  min-width: 550px;
}
@media (max-width: 600px) {
  .widerDialog {
    min-width: 430px;
  }
}
@media (max-width: 480px) {
  .widerDialog {
    min-width: 75%;
  }
}

.close {
  position: absolute;
  top: 0.8rem;
  right: 0.8rem;
}
.close i {
  font-size: 1rem;
  cursor: pointer;
}

.header {
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.header h2 {
  cursor: default;
  line-height: 1.1;
  letter-spacing: normal;
  margin: 0;
  overflow-wrap: anywhere;
}
@media (max-width: 1024px) {
  .header h2 {
    font-size: 1.5rem;
  }
}
@media (max-width: 600px) {
  .header h2 {
    font-size: 1.25rem;
  }
}
.header :global i {
  font-size: 3rem;
}
.header :global i.icon-info {
  color: #4a009e;
}
.header :global i.icon-warning {
  color: #f05400;
}
.header :global i.icon-success {
  color: #008f62;
}
.header :global i.icon-error {
  color: #bd0b23;
}

.headerLeft {
  composes: header;
  align-items: flex-start;
}

.headerResponsiveHorizontalAlign {
  composes: header;
  flex-direction: row;
  justify-content: center;
}
@media (max-width: 600px) {
  .headerResponsiveHorizontalAlign {
    flex-direction: column;
  }
}

.body {
  margin: 1rem 0;
  overflow: auto;
}

.footer {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
}
@media (max-width: 600px) {
  .footer {
    flex-direction: column;
    gap: 1rem;
  }
  .footer button {
    width: 100%;
    text-align: center;
  }
  .footer button span {
    justify-content: center;
  }
}

.noMarginBottom {
  margin-bottom: 0;
}