@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.thumbnail {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #d1d6e0;
  min-width: 4rem;
  max-width: 4rem;
  height: 5rem;
  border-radius: 0.125rem;
  border: 1px solid #d1d6e0;
}
.thumbnail img {
  max-height: 100%;
  max-width: 100%;
}

.fallback {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.625rem;
  font-weight: 600;
  line-height: 0.875rem;
  color: rgba(10, 16, 26, 0.8);
  height: 100%;
  width: 50px;
}

.whiteBackground {
  background-color: #fff;
}