@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.wrapperWithBgImage {
  background-image: var(--mobileImage);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
@media (min-width: 600px) {
  .wrapperWithBgImage {
    background-image: var(--image);
  }
}
@media (-webkit-min-device-pixel-ratio: 1.3) {
  .wrapperWithBgImage {
    background-image: var(--mobileRetinaImage, var(--image));
  }
}
@media (-webkit-min-device-pixel-ratio: 1.3) and (min-width: 600px) {
  .wrapperWithBgImage {
    background-image: var(--retinaImage, var(--image));
  }
}

.wrapperWithBgColor {
  background: var(--bgColor, "red");
}