@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* CSS reset for input type range for all browsers as base styles are different */
input[type=range] {
  -webkit-appearance: none; /* Hides the slider so that custom slider can be made */
  width: 100%; /* Specific width is required for Firefox. */
  background: transparent; /* Otherwise white in Chrome */
}
input[type=range]:focus {
  outline: none;
}

/* end */
/* SCSS nesting and even styling multiple elements using comma isn't working for -webkit and -moz pseudoselectors */
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 11px;
  width: 11px;
  border-radius: 50%;
  background-color: #000083;
  cursor: pointer;
  margin-top: -3px;
}

input[type=range]::-moz-range-thumb {
  height: 11px;
  width: 11px;
  border-radius: 50%;
  background-color: #000083;
  cursor: pointer;
}

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 5px;
  cursor: pointer;
  background: #dce0eb;
  border-radius: 5px;
}

input[type=range]::-moz-range-track {
  width: 100%;
  height: 5px;
  cursor: pointer;
  background: #dce0eb;
  border-radius: 5px;
}

.container {
  display: flex;
  align-items: center;
}

.number {
  height: 32px;
  width: 36px;
  text-align: center;
  border: 1px solid #aeb6c2;
  border-radius: 2px;
  font-size: 1rem;
  margin: 0 0.25rem 0 0.5rem;
  outline: none;
}
.number:focus {
  color: #000083;
  border-color: #000083;
}

.suffix {
  color: rgba(10, 16, 26, 0.7);
  font-size: 0.875rem;
}