@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.message {
  position: relative;
  border: 1px solid;
  border-radius: 0.125rem;
  display: flex;
  justify-content: flex-start;
  padding: 1rem;
  margin-bottom: 1.5rem;
}
.message.error {
  border-color: #bd0b23;
  background-color: rgba(214, 34, 58, 0.1);
}
.message.success {
  border-color: #008f62;
  background-color: rgba(0, 168, 115, 0.1);
}
.message.warning {
  border-color: #f05400;
  background-color: rgba(255, 117, 0, 0.1);
}
.message.neutral {
  border-color: #6313bd;
  background-color: rgba(99, 19, 189, 0.1);
}
.message.marketing {
  border-color: #a41cad;
  background-color: rgba(164, 28, 173, 0.1);
}
.message.transparent {
  background-color: transparent;
}
.message p {
  color: rgba(10, 16, 26, 0.7);
  line-height: 1.25;
  margin: 0;
  margin-bottom: 0.5rem;
}
.message .messageTitle {
  color: #0a101a;
}
.message button {
  margin-top: 0.5rem;
}
.message .icon {
  margin-right: 1rem;
}

.close {
  position: absolute;
  top: 0.9rem;
  right: 0.9rem;
}
.close i {
  font-size: 1rem;
  cursor: pointer;
}