@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.withTopBorder {
  border-top: 1px solid #aeb6c2;
}

.footer {
  padding: 1rem 2rem;
  font-family: "museo-sans", sans-serif;
}
.footer .itemizedCartCosts {
  display: grid;
  grid-template-columns: 1fr 1.4fr;
  gap: 0.25rem;
}
.footer .itemizedCartCosts span {
  font-size: 0.75rem;
  line-height: 135%;
}
.footer .itemizedCartCosts .label {
  font-weight: 700;
}
.footer .itemizedCartCosts .bulkDiscountLabel {
  color: rgba(10, 16, 26, 0.6);
  margin-left: 1rem;
}
.footer .itemizedCartCosts .bulkDiscountAmount {
  font-weight: 300;
  color: rgba(10, 16, 26, 0.6);
}
.footer .itemizedCartCosts .amount {
  font-weight: 300;
}
.footer .itemizedCartCosts .subtotal {
  font-size: 1rem;
  font-weight: 900;
}
.footer .itemizedCartCosts *:nth-child(even) {
  text-align: right;
}
.footer .actions {
  margin-top: 1rem;
  display: flex;
  gap: 1rem;
  flex-direction: column;
}
.footer .actions button {
  font-weight: 600;
}