@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.wrapper {
  border: 1px solid #aeb6c2;
  border-radius: 2px;
  position: relative;
  display: flex;
}
.wrapper.withError {
  border-color: #bd0b23;
}
.wrapper.isDisabled {
  border-style: dashed;
  background-color: #e9edf6;
}
.wrapper.isDisabled input {
  color: rgba(10, 16, 26, 0.6);
}
.wrapper.isDisabled input:focus ~ .label.hideWhenDisabled, .wrapper.isDisabled input:not(:placeholder-shown) ~ .label.hideWhenDisabled, .wrapper.isDisabled input:-webkit-autofill + label {
  display: none;
}
.wrapper .label {
  color: rgba(10, 16, 26, 0.6);
  position: absolute;
  pointer-events: none;
  left: 1rem;
  top: 1rem;
  transition: 0.2s ease all;
  text-transform: none;
  font-size: 1rem;
  font-weight: 500;
  letter-spacing: unset;
}
.wrapper input {
  top: 0;
  left: 0;
  padding: 1rem;
  border: none;
  outline: none;
  display: block;
  width: 100%;
}
.wrapper input::placeholder {
  color: transparent;
  transition: 0.2s ease all;
}
.wrapper input:focus, .wrapper input:not(:placeholder-shown) {
  outline: none;
}
.wrapper input:focus::placeholder, .wrapper input:not(:placeholder-shown)::placeholder {
  color: rgba(10, 16, 26, 0.6);
}
.wrapper input:focus ~ .label, .wrapper input:not(:placeholder-shown) ~ .label, .wrapper input:-webkit-autofill + label {
  top: -0.5rem;
  left: 0.5rem;
  background-color: #fff;
  padding-inline: 0.5rem;
  font-size: 0.75rem;
}
.wrapper input:-webkit-autofill, .wrapper input:-webkit-autofill:hover, .wrapper input:-webkit-autofill:focus, .wrapper input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px #fff inset;
}
.wrapper .toggleShowPassword {
  display: flex;
  align-items: center;
  padding-right: 1rem;
  background-color: #fff;
  color: #000083;
  cursor: pointer;
}

.applyInputWrapper {
  display: flex;
}
.applyInputWrapper .inputWrapper {
  flex-grow: 1;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.applyInputWrapper .inputWrapper.applied {
  border-style: dashed;
}
.applyInputWrapper .inputWrapper.applied label {
  background-color: #e9edf6;
}
.applyInputWrapper .inputWrapper input {
  background-color: #fff;
  height: 46px;
}
.applyInputWrapper .inputWrapper input:disabled {
  color: rgba(10, 16, 26, 0.6);
  background-color: #e9edf6;
}